<script>

import DropDown from './DropDown';
// import DropDown from './CustomDropDown';
import constants from '../constants';


export default {
    name: 'Player',
    components: {
        DropDown,
    },
    props: {
        'player': {
            type: Object,
            required: true,
        },
        'isHost': {
            type: Boolean,
            required: false,
        },
        'userIsHost': {
            type: Boolean,
            required: false,
        },
        'openColors': {
            type: Array,
            required: false,
            default: () => ([]),
        },
    },
    data() {
        return {
            userString0: 'Kick',
            userString1: 'Close',
            userString2: 'Computer',
            userString3: 'Reserve',
            userString4: 'Ban',
            raceOptions: constants.RACE_SELECTION,
        };
    },
    computed: {
        playerOptions() {
            return [this.userString0, this.userString1, this.userString2, this.userString3, this.userString4, this.player.name];
        },
        defaultIndex() {
            return this.playerOptions.length - 1;
        },
        defaultColorIndex() {
            return this.displayPlayerColors.length - 1;
        },
        displayPlayerColors() {
            return this.openColors.concat([this.player.color]);
        },
        isUser() {
            return this.$store.state.userSession.userId === this.player.id;
        },
        isAI() {
            return this.player.playerType === 'ai';
        },
        selectable() {
            return this.isUser || (this.isAI && this.userIsHost);
        },
    },
    methods: {
        hostPlayerSlotSelection(selectionIndex) {
            this.$emit('hostPlayerSlotSelection', { selectionIndex, id: this.player.id });
        },
        updatePlayerColor(colorIndex) {
            const color = this.openColors[colorIndex];
            this.$store.commit('updatePlayerColor', { id: this.player.id, color });
        },
        updatePlayerRace(raceIndex) {
            const race = this.raceOptions[raceIndex];
            this.$store.commit('updatePlayerRace', { id: this.player.id, race });
        },
    },
};

</script>
<template>
<div class="player-lobby">
    <DropDown
        v-if="userIsHost"
        ref="playerSlot"
        class="player-name"
        :options="playerOptions"
        :default="defaultIndex"
        :hideLabel="true"
        :useDefaultOverride="true"
        :selectStyle="{
            width: '200px',
        }"
        @select="hostPlayerSlotSelection">
    </DropDown>
    <div
        v-else
        class="player-name no-select">
        {{ player.name }}
    </div>
    <DropDown
        v-if="selectable"
        ref="raceSlot"
        class="race-selection"
        :hideLabel="true"
        :default="raceOptions.indexOf(player.race)"
        :useDefaultOverride="true"
        :options="raceOptions"
        @select="updatePlayerRace"
        >
    </DropDown>
    <div
        v-else
        class="race-selection no-select">
        {{ player.race }}
    </div>
    <DropDown
        v-if="selectable"
        ref="colorSelection"
        class="color-selection"
        :hideLabel="true"
        :default="defaultColorIndex"
        :options="displayPlayerColors"
        :useDefaultOverride="true"
        @select="updatePlayerColor">
    </DropDown>
    <div
        v-else
        class="color-selection no-select">
        {{ player.color }}
    </div>
</div>
</template>
