<script>

import outsetBox from './outsetBox';

export default {
    name: 'wood-frame',
    components: {
        'outset-box': outsetBox,
    },
    props: ['width', 'height'],
    data() {
        return {
            xNum: 6,
            yNum: 2,
        };
    },
    computed: {
        spacing() {
            return (this.width / (this.xNum - 1));
        }
    },
    methods: {
        verticalTransform(index) {
            if (index <= (this.yNum * 0.5)) {
                return `translate3d(0, calc(-${index}00%), 0)`;
            }
            const usedIndex = Math.ceil(index - (this.yNum * 0.5));
            return `translate3d(0, calc(${usedIndex}00%), 0)`;
        }
    },
};

</script>
<template>
<div class="wood-frame" :style="{ width: `${width}px`,  height: `${height}px` }">
    <div class="top-rect wood-horizontal">
        <outset-box class="corner"></outset-box>
        <outset-box v-for="index in xNum - 2"
            :class="{ 'corner': (index === xNum - 1) }"
            :style="{
                transform: `translate3d(calc(${(index + 0) * spacing}px - 50%), 0, 0)`,
                'z-index': 2,
                'position': 'absolute',
            }"
            :key="index">
        </outset-box>
        <outset-box class="corner"
            :style="{
                transform: `translate3d(calc(${width}px - 100%), 0, 0)`,
                'z-index': 2,
                'position': 'absolute',
            }">
        </outset-box>
    </div>
    <div class="mid-rect">
        <div class="left-rect wood-vertical">
            <outset-box v-for="index in yNum"
                :style="{
                    transform: verticalTransform(index),
                    'z-index': 2,
                    'position': 'absolute',
                }"
                :key="index">
            </outset-box>
        </div>
        <div class="content-container">
            <slot></slot>
        </div>
        <div class="right-rect wood-vertical">
            <outset-box v-for="index in yNum"
                :style="{
                    transform: verticalTransform(index),
                    'z-index': 2,
                    'position': 'absolute',
                }"
                :key="index">
            </outset-box>
        </div>
    </div>
    <div class="bot-rect wood-horizontal">
        <outset-box class="corner"></outset-box>
        <outset-box v-for="index in xNum - 2"
            :class="{ 'corner': (index === xNum - 1) }"
            :style="{
                transform: `translate3d(calc(${(index + 0) * spacing}px - 50%), 0, 0)`,
                'z-index': 2,
                'position': 'absolute',
            }"
            :key="index">
        </outset-box>
        <outset-box class="corner"
            :style="{
                transform: `translate3d(calc(${width}px - 100%), 0, 0)`,
                'z-index': 2,
                'position': 'absolute',
            }">
        </outset-box>
    </div>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';

$wood-col-width: 25px;

.corner {
    z-index: 2;
    width: 32px;
    height: 27px;
    position: absolute;
}

.wood-horizontal {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    background: url('../static/imgs/wood-grain-weathered-cropped.jpg') rgba(97, 45, 29, 0.7);
    background-size:cover;
    background-blend-mode: multiply;
}
.wood-vertical {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 100%;
    background: url('../static/imgs/wood-grain-weathered-cropped-vertical.jpg') rgba(97, 45, 29, 0.7);
    background-size:cover;
    background-blend-mode: multiply;
}

.wood-frame {
    background-color: rgba(92, 91, 90, 0.75);
    display: flex;
    flex-direction: column;
    overflow-x: visible;
    overflow-y: visible;
    .top-rect {
        height: $wood-col-width;
    }
    .mid-rect {
        display: flex;
        flex-direction: row;
        .left-rect {
            width: $wood-col-width + 5;
        }
        .content-container{
            display: flex;
            margin-left: auto;
            flex-grow: 2;
            margin-right: auto;
            height: 100%;
            justify-content: center;
        }
        .right-rect {
            width: $wood-col-width + 5;
        }
    }
    .bot-rect {
        height: $wood-col-width;
    }
}

</style>
