import Tracking from '../../services/tracking';
import DGAB from './detectGABlock';
import './detectAdBlock';

/*eslint-disable no-undef */
const staticJsBase = '/static/js/';

DGAB.injectGA(window, document, 'script', `${staticJsBase}stuff.min.js`, 'ga');

const InitTrackingPlugin = {
    install(Vue, config) {
        window.torrentCount = 0;
        //cross domain setup & tracker instantiation
        Vue.prototype.$Tracking = Promise.all([
            this.asyncDetectGABlock(Vue, config),
        ]).then(() => {
            Vue.prototype.$Tracking = new Tracking(config);
            Vue.prototype.$Tracking.createGATracker(GA_PRODUCT_CODE);
        });
    },
    asyncDetectGABlock(Vue, config) {
        const gaBlock = new Promise((resolve) => {
            DGAB.detectGABlock().then((gaBlocked) => {
                config.gaBlockerDetected = gaBlocked;
                resolve();
            });
        });
        return gaBlock;
    },
};

export default InitTrackingPlugin;
