<script>

import HealthBar from './HealthBar';
import ManaBar from './ManaBar';
import utils from '../utils';
import constants from '../constants';
import unitSoundInfo from '../units/unitSoundInfo';

export default {
    name: 'unit',
    components: {
        HealthBar,
        ManaBar,
    },
    props: {
        'attributes': {
            type: Array,
            required: true,
        },
        'unitId': {
            type: String,
            required: true,
        },
        'actionSelected': {
            type: Object,
            required: false,
            default: () => ({}),
        },
        'pendingAction': {
            type: String,
            required: false,
        },
        'isEnemy': {
            type: Boolean,
            required: false,
        },
        'isUserUnit': {
            type: Boolean,
            required: false,
        },
        'isSelected': {
            type: Boolean,
            required: false,
        },
        'selectedUnitId': {
            type: String,
            required: false,
        },
        'gameUnit': {
            type: Object,
            required: true,
            default: () => ({}),
        },
        'hitPointsMaximumOverride': {
            type: Number,
            required: false,
        },
        'manaPointsMaximumOverride': {
            type: Number,
            required: false,
            default: 0,
        },
        'abilitiesInfo': {
            type: Object,
            required: false,
            default: () => ({ abilities: {} }),
        },
        'color': {
            type: String,
            required: false,
            default: () => (''),
        },
        // 'hidden': {
        //     type: Number,
        //     required: false,
        //     default: () => (0),
        // },
        'allMods': {
            type: Array,
            required: false,
            default: () => ([]),
        }
    },
    data() {
        return {
            attributeHeaders: this.$store.state.attributeHeaders,
            frameStyle: {
                'background-image': '',
            },
            yesCount: 1,
            yesAttackCount: 1,
            whatCount: 1,
            pissedCount: 1,
            previousAction: '',
            unitTypeModifier: '',
            default: 'what',
            colors: {
                red: 'rgba(255, 0, 0, 79)',
                green: 'rgba(0, 255, 0, 79)',
                blue: 'rgba(0, 0, 255, 0.79)',
                pink: 'rgba(255, 192, 203, 0.79)',
                purple: 'rgba(128,0,128, 0.79)',
                indigo: 'rgba(75,0,130, 0.79)',
                magenta: 'rgba(255, 0, 255, 0.79)',
                yellow: 'rgba(255,255,0, 0.79)',
                orange: 'rgba(255, 165, 0, 0.79)',
                gold: 'rgba(255,215,0, 0.79)',
                tomato: 'rgba(255, 99, 71, 0.79)',
                orangered: 'rgba(255, 69, 0, 0.79)',
                coral: 'rgba(255, 127, 80, 0.79)',
                white: 'rgba(255, 255, 255, 0.79)',
                grey: 'rgba(128, 128, 128, 0.79)',
                silver: 'rgba(192, 192, 192, 0.79)',
                black: 'rgba(0, 0, 0, 0.79)',
                lime: 'rgba(0, 255, 0, 0.79)',
                springgreen: 'rgba(0, 255, 127, 0.79)',
                teal: 'rgba(0, 128, 128, 0.79)',
                turquoise: 'rgba(64, 224, 208, 0.79)',
                aqua: 'rgba(0, 255, 255, 0.79)',
                darkcyan: 'rgb(0, 139, 139, 0.79)',
                darkturquoise: 'rgb(0,206,209, 0.79)',
            },
            // hidden: 0,
        };
    },
    computed: {
        abilities() {
            return this.abilitiesInfo.abilities;
        },
        canAttack() {
            return this.abilitiesInfo.canAttack;
        },
        portrait() {
            return this.gameUnit.tempSrc || this.getAttribute('portrait');
        },
        portraitStyle() {
            let src = this.portraitSrc;
            if (!src) {
                src = 'static/imgs/missing_portrait.jpg';
            }
            return {
                'background-image': `url(${this.portrait})`,
            };
        },
        soundInfo() {
            const info = unitSoundInfo[this.camelCaseName];
            if (info.alias) {
                return unitSoundInfo[info.alias] || info;
            }
            return info;
        },
        camelCaseName() {
            return this.name.replace(/\s/g, '');
        },
        unitSoundPath() {
            const info = unitSoundInfo[this.camelCaseName];
            const usedName = info.alias || this.camelCaseName;
            return `${constants['SOUNDS_UNITS'][this.race]}/${usedName}/${usedName}`;
        },
        // attributes
        name() {
            return utils.getNameFromEnum(this.getAttribute('unit'));
        },
        // gold() {
        //     return this.getAttribute('gold');
        // },
        // lumber() {
        //     return this.getAttribute('lumber');
        // },
        // supply() {
        //     return this.getAttribute('supply');
        // },
        //default starting value
        hitPoints() {
            return this.gameUnit.hitPoints;// || this.getAttribute('hit_points');
        },
        hitPointsMaximum() {
            return this.hitPointsMaximumOverride || this.getAttribute('hit_points');
        },
        manaPoints() {
            return this.gameUnit.manaPoints;
        },
        manaPointsMaximum() {
            return this.manaPointsMaximumOverride || this.gameUnit.manaPointsMax;
        },
        // armorType() {
        //     return this.getAttribute('armor_type');
        // },
        // armor() {
        //     return this.getAttribute('armor');
        // },
        // armorMaximum() {
        //     return this.getAttribute('armor_maximum');
        // },
        // daySight() {
        //     return this.getAttribute('day_sight');
        // },
        // nightSight() {
        //     return this.getAttribute('night_sight');
        // },
        // speed() {
        //     return this.getAttribute('speed');
        // },
        // buildTime() {
        //     return this.getAttribute('build_time');
        // },
        // attackType() {
        //     return this.getAttribute('ground_attack_type');
        // },
        // weaponType() {
        //     return this.getAttribute('ground_weapon_type');
        // },
        // groundAttack() {
        //     return this.getAttribute('ground_attack');
        // },
        // groundAttackMaximum() {
        //     return this.getAttribute('ground_attack_maximum');
        // },
        // secondGroundAttack() {
        //     return this.getAttribute('second_ground_attack');
        // },
        // secondGoundAttackMaximum() {
        //     return this.getAttribute('second_ground_attack_maximum');
        // },
        // airAttack() {
        //     return this.getAttribute('air_attack');
        // },
        // airAttackMaximum() {
        //     return this.getAttribute('air_attack_maximum');
        // },
        // cooldown() {
        //     return this.getAttribute('cooldown');
        // },
        // range() {
        //     return this.getAttribute('range');
        // },
        // rangeMaxmimum() {
        //     return this.getAttribute('range_maximum');
        // },
        race() {
            return this.getAttribute('race');
        },
        animation() {
            return this.gameUnit.tempSrc || this.getAttribute('animation');
        },
        hiddenMods() {
            // return this.gameUnit.getAttributeModifier('hidden');
            return this.allMods.filter(mod => mod.attributeType === 'hidden');
        },
        hidden() {
            return this.hiddenMods.reduce((acc, mod) => {
                if (mod.value) {
                    return acc + mod.value;
                }
                return acc;
            }, 0) + this.getAttribute('hidden');
        },
        unitStyle() {
            const style = {};
            if (this.isEnemy && this.pendingAction === 'attack') {
                style.cursor = `url(static/imgs/${'target-enemy'}.cur), auto`;
            }
            if (this.hidden > 0) {
                style.opacity = 0.5;
            }
            style['box-shadow'] = `2px 0px 3px 3px ${this.colors[this.color]}`;
            return style;
        },
    },
    created() {
        this.setFrameSrc(this.portrait);
    },
    watch: {
        portrait() {
            this.setFrameSrc(this.portrait);
        },
    },
    methods: {
        setFrameSrc(src) {
            this.frameStyle['background-image'] = `url(${src})`;
        },
        getAttribute(enumName) {
            return this.attributes[this.attributeHeaders.headerEnums[enumName]];
        },
        resetOtherResponseCounts(actionType) {
            const self = this;
            ['pissed'].forEach((action) => {
                if (action !== actionType) {
                    self[`${action}Count`] = 1;
                }
            });
        },
        getUnitResponsePath(actionType) {
            if (actionType === '') {
                return this.unitSoundPath;
            }
            let usedActionType = actionType;
            let actionCount = this[`${actionType}Count`];
            if (actionType === 'what' || actionType === 'yesAttack') {
                // certain units cant attack and therefore do not have yesAttack responses
                if (!this.soundInfo[`${actionType}Max`] && actionType === 'yesAttack') {
                    usedActionType = 'what';
                }
                actionCount = utils.getRandomInt(this.soundInfo[`${usedActionType}Max`], 1);
            }
            const actionPath = `${utils.upperCaseFirstChar(usedActionType)}${actionCount}.wav`;
            return `${this.unitSoundPath}${actionPath}`;
        },
        getUnitSoundPath(unitName) {
            return `${constants['SOUNDS_UNITS'][this.race]}/${unitName}/${unitName}`;
        },
        playWhat() {
            if (this.soundInfo.whatMin === null || this.soundInfo.whatMin === undefined) {
                return Promise.resolve();
            }
            return this.$playSound(this.getUnitResponsePath('what'));
        },
        playPissed() {
            if (this.soundInfo.pissedMin === null || this.soundInfo.pissedMin === undefined) {
                return this.playWhat();
            }
            return this.$playSound(this.getUnitResponsePath('pissed'));
        },
        playYes() {
            if (this.soundInfo.yesMin === null || this.soundInfo.yesMin === undefined) {
                return Promise.resolve();
            }
            return this.$playSound(this.getUnitResponsePath('yes'));
        },
        playYesAttack() {
            if (this.soundInfo.yesAttackMin === null) {
                return this.playYes();
            }
            return this.$playSound(this.getUnitResponsePath('yesAttack'));
        },
        playDeath() {
            if (this.soundInfo.death === null) {
                return Promise.resolve();
            }
            const death = 'Death.wav';
            if (this.soundInfo.death === undefined) {
                return this.$playSound(this.unitSoundPath + death);
            }
            return this.$playSound(this.getUnitSoundPath(this.soundInfo.death) + death);
        },
        playReady() {
            if (this.soundInfo.ready === null) {
                return Promise.resolve();
            }
            const ready = 'Ready1.wav';
            if (this.soundInfo.ready === undefined) {
                return this.$playSound(this.unitSoundPath + ready);
            }
            return this.$playSound(this.getUnitSoundPath(this.soundInfo.ready) + ready);
        },
        playWarCry() {
            if (this.soundInfo.warcry === null) {
                return Promise.resolve();
            }
            const warcry = 'Warcry1.wav';
            if (this.soundInfo.warcry === undefined) {
                return this.$playSound(this.unitSoundPath + warcry);
            }
            return this.$playSound(this.getUnitSoundPath(this.soundInfo.warcry) + warcry);
        },
        incrementActionCount(action) {
            let usedAction = action;
            if (action === 'attack') {
                usedAction = 'yesAttack';
            }
            let cur = this[`${usedAction}Count`];
            if (cur + 1 > this.soundInfo[`${usedAction}Max`]) {
                cur = this.soundInfo[`${usedAction}Min`];
            } else {
                cur++;
            }
            this[`${usedAction}Count`] = cur;
            return cur;
        },
        click() {
            if (this.pendingAction && this.pendingAction !== 'what' & this.pendingAction !== 'pissed') {
                this.$emit('targetMe', this.unitId);
                return;
            } else if (this.isEnemy) {
                this.$emit('click', { unitId: this.unitId });
                return;
            }
            // action type:
            // Ready, Yes, What, Pissed, Warcry, Attack = YesAttack
            // Yes - Cast Spell, Stop, Hold Position, Move
            // YesAttack - Attack specific unit
            // Warcry - Attack to location
            // What - responding to select (1st time)
            // Pissed - responding to select (nth consecutive)
            let action;
            const eventData = {};
            if (this.selectedUnitId !== this.unitId) {
                // when going from one unit to another, all response counts
                // should be set back to zero
                this.resetOtherResponseCounts();
                action = 'what';
                this.previousAction = null;
            } else {
                action = this.pendingAction || this.default;
            }
            let usedAction = action;
            if (action === 'attack') {
                usedAction = 'yesAttack';
            }
            if (action === this.previousAction) {
                if (action === 'what') {
                    usedAction = 'pissed';
                    action = usedAction;
                } else if (action === 'pissed' && this.pissedCount === this.soundInfo.pissedMax) {
                    usedAction = 'what';
                    action = usedAction;
                }
            }
            eventData.action = action;
            eventData.unitId = this.unitId;
            // the only response sound that battle needs from the unit because of the count state
            eventData.unitActionSoundPath = this.getUnitResponsePath('');
            eventData.soundInfo = this.soundInfo;
            this.incrementActionCount(usedAction);
            this.resetOtherResponseCounts(usedAction);
            this.setFrameSrc(this.animation);
            this.$emit('click', eventData);
            this.previousAction = action;
            const self = this;
            return this[`play${utils.upperCaseFirstChar(usedAction)}`]().then(() => {
                self.setFrameSrc(self.portrait);
            }).catch(() => {
                self.setFrameSrc(self.portrait);
            });
        },
        beforeDestroy() {
            // these never fire
            if (this.hitPoints <= 0) {
                this.playDeath();
            }
        },
        destroy() {
            // these never fire
            if (this.hitPoints <= 0) {
                this.playDeath();
            }
        },
        deactivated() {
            // these never fire
            if (this.hitPoints <= 0) {
                this.playDeath();
            }
        }
    },
};

</script>
<template>
<div class="unit"
    @click="click"
    :class="{ 'selected': isSelected, 'enemy': isEnemy, 'user': isUserUnit }"
    :style="unitStyle">
    <div class="name">
        {{name}}
    </div>
    <div class="portrait-container" :style="frameStyle"></div>
    <HealthBar
      :min="0"
      :now="hitPoints"
      :max="hitPointsMaximum">
    </HealthBar>
    <ManaBar v-if="manaPointsMaximum > 0"
        :min="0"
        :now="manaPoints"
        :max="manaPointsMaximum">
    </ManaBar>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';
@import '../stylesheets/units';

.selected {
    box-shadow: 3px 0px 15px 12px rgba(237,237,237,1) !important;
}
.selected.enemy {
    box-shadow: 2px 0px 15px 15px rgba(194, 51, 41, 0.79) !important;
}
.enemy {
    // box-shadow: 2px 0px 3px 3px rgba(194, 51, 41, 0.79);
    &:hover {
        box-shadow: 2px 0px 15px 15px rgba(194, 51, 41, 0.79) !important;
    }
}
.user {
    // box-shadow: 0px 0px 3px 3px rgba(237,237,237, 0.79);
    &:hover {
        box-shadow: 0px 0px 10px 10px rgba(237,237,237,1) !important;
    }
}

.unit {
    display: flex;
    flex-direction: column;
    border: 20px solid transparent;
    padding: 15px;
    border-image: url("../static/imgs/wood.jpg") 40 round;
    border-radius: 5px;
    margin-top: 20px;
    margin-bottom: 20px;
    .name {
        background-color: black;
        color: white;
        display: flex;
        justify-content: center;
        padding: 3px;
        word-wrap: break-word;
        max-width: 120px;
        min-width: 110px;
    }
    .portrait-container {
        min-width: 100px;
        min-height: 100px;
        background-size: contain;
        background-repeat: no-repeat;
    }
}

</style>
