import Human from './humans';
import Orc from './orcs';
import Undead from './undead';
import Nightelf from './nightelves';
import all from './all';
import utils from '../utils';

const headerNames = all.headers;
const headerEnums = all.headerEnums;
const headerEnumsList = headerNames.map(headerName => utils.getEnumFromName(headerName));

const bla = {
    races: ['Human', 'Orc', 'Undead', 'Nightelf'],
    unitRaces: ['Human', 'Orc', 'Undead', 'Nightelf', 'Demon', 'Creeps', 'Critters'],
    Human,
    Orc,
    Undead,
    Nightelf,
    headerNames,
    headerEnumsList,
    headerEnums,
};

const obj = {
    getRange(max) {
        return [...Array(max).keys()];
    },
    getRandomInt(max) {
        return Math.floor(Math.random() * Math.floor(max));
    },
    getRandomRace() {
        return bla.races[bla.getRandomInt(4)];
    },
    getRandomUnitIndex(race) {
        let usedRace = race || bla.races[bla.getRandomInt(4)];
        usedRace = bla[usedRace];
        const unitsRange = usedRace.units.length;
        const possibleChoices = bla.getRange(unitsRange).filter(n => usedRace.summon.includes(n));
        return possibleChoices[bla.getRandomInt(possibleChoices.length)];
    },
    getRandomUnitId(race) {
        let usedRace;
        if (race === undefined) {
            usedRace = bla.getRandomRace();
        } else if (Number.isInteger(race)) {
            usedRace = bla.races[race];
        } else if (bla.races.includes(race)) {
            usedRace = race;
        } else {
            //eslint-disable-next-line no-throw-literal
            throw `getRandomUnitId: expected @param race to be valid race string or integer, instead got: ${race}`;
        }
        const usedRaceUnits = bla[usedRace].units;
        const unitsRange = usedRaceUnits.length;
        const possibleChoices = bla.getRange(unitsRange).filter(n => !(bla[usedRace].summon.includes(n)));
        const index = possibleChoices[bla.getRandomInt(possibleChoices.length)];
        return `${usedRace}_${index}`;
    },
    getAttributeEnum(enumName) {
        return bla.headerEnums[enumName];
    },
    getUnitAttributes(race, unitIndex) {
        const err = new Error('getUnitAttributes');
        if (!race) {
            err.message = `Invalid param @race: ${race}`;
            throw err;
        }
        if (!unitIndex) {
            err.message = `Invalid param @unitIndex: ${unitIndex}`;
            throw err;
        }
        const raceObj = bla[race];
        if (!raceObj) {
            err.message = `No race was found for race: ${race}`;
            throw err;
        }
        const unit = raceObj.units[unitIndex];
        if (!unit) {
            err.message = `No unit was found for race: ${race}, index: ${unitIndex}`;
            throw err;
        }
        return unit;
    },
    getUnitAttribute(race, unitIndex, enumName) {
        const usedRace = typeof race === 'string' ? race : bla.races[race];
        const attriubteEnum = bla.getAttributeEnum(enumName);
        return bla.getUnitAttributes(usedRace, unitIndex)[attriubteEnum];
    },
    /*eslint-disable camelcase*/
    getUnitAttributesById(race_unitIndex) {
        //TODO - find better way to deal with this
        // if (race_unitIndex === 'length') {
        //     return;
        // }
        if (race_unitIndex === undefined) {
            //eslint-disable-next-line no-throw-literal
            throw `getUnitAttributesById: expected @param race_unitIndex to be string with underscore, instead got: ${race_unitIndex}`;
        }
        const info = race_unitIndex.split('_');
        return bla.getUnitAttributes(info[0], info[1]);
    },
    getUnitAttributeById(race_unitIndex, enumName) {
        // //TODO - find better way to deal with this
        // if (race_unitIndex === 'length') {
        //     return;
        // }
        if (race_unitIndex === undefined) {
            //eslint-disable-next-line no-throw-literal
            throw `getUnitAttributeById: expected @param race_unitIndex to be string with underscore, instead got: ${race_unitIndex}`;
        }
        const info = race_unitIndex.split('_');
        return bla.getUnitAttribute(info[0], info[1], enumName);
    }
};

Object.keys(obj).forEach((key) => {
    bla[key] = obj[key];
});

export default bla;

