let race;
let unitNames;
race = 'Human';
unitNames = unitIndex[race].unit_names;
unitNames.forEach((name, index) => {
    const usedName = name.replace(/\s/g, '');
    const info = unitAbilityInfo[usedName];
    unitAbilityInfo[`${race}_${index}`] = info;
});
