// inject tracking plugin
Vue.use(initTracking, config);
Vue.prototype.$dev = DEV;
Vue.prototype.$store = store;
Vue.prototype.$trackWhenLoad = function installHook(callback) {
    if (this.$Tracking.then && this.$Tracking.then instanceof Function) {
        this.$Tracking.then(() => {
            callback();
        });
    } else {
        callback();
    }
};
Vue.prototype.$playSound = function playSound(src, optionsFunc) {
    return new Promise((resolve) => {
        const audio = new Audio(src);
        if (typeof optionsFunc === 'function') {
            optionsFunc(audio);
        }
        audio.addEventListener('ended', resolve);
        audio.addEventListener('error', resolve);
        audio.play();
    });
};
Vue.prototype.$playSoundForDuration = function playSoundForDuration(src, duration = 1000) {
    function startTimer(audio) {
        audio.loop = 'loop';
        setTimeout(() => {
            audio.pause();
            audio.src = '';
        }, duration);
    }
    return Vue.prototype.$playSound(src, startTimer);
};
Vue.prototype.$getSoundPlayer = function(src) {
    const a = new Audio(src);
    a.preload = 'auto';
    return a;
};
//eslint-disable-next-line no-unused-vars
const vm = new Vue({
    el: '#app-container',
    render: h => h(App),
});
