
import Vue from 'vue';
/*eslint-disable no-shadow*/

const state = {
    global: {
        value: 1.0,
        muted: false,
    },
    soundEffects: {
        value: 1.0,
        muted: false,
    },
    music: {
        value: 0.2,
        muted: false,
        src: 'static/sounds/War3XMainScreen.mp3',
    },
    battleTracks: ['static/sounds/battle/battle06.mp3'],
    mainScreenTracks: ['static/sounds/War3XMainScreen.mp3'],
};

const mutations = {
    saveVolume(state, { volumeType, value, muted }) {
        Vue.set(state[volumeType], 'value', value);
        Vue.set(state[volumeType], 'muted', muted);
    },
    playBattleMusic(state) {
        Vue.set(state.music, 'src', state.battleTracks[0]);
    },
};

const actions = {};

export default {
    state,
    mutations,
    actions
};
