import Vue from 'vue';
import utils from '../utils';

const second = 1000;

function addUnitHealth(unitId, hitPoints, gameStateUnits) {
    const unit = gameStateUnits[unitId].gameUnit;
    Vue.set(unit, 'hitPoints', unit.hitPoints + hitPoints);
    if (unit.hitPoints > unit.hitPointsMax) {
        Vue.set(unit, 'hitPoints', unit.hitPointsMax);
    }
}
function removeUnitHealth(unitId, hitPoints, gameStateUnits) {
    const unit = gameStateUnits[unitId].gameUnit;
    Vue.set(unit, 'hitPoints', unit.hitPoints - hitPoints);
    if (unit.hitPoints < 0) {
        Vue.set(unit, 'hitPoints', 0);
    }
}
//eslint-disable-next-line
function addUnitMana(unitId, manaPoints, gameStateUnits) {
    const unit = gameStateUnits[unitId].gameUnit;
    Vue.set(unit, 'manaPoints', unit.manaPoints + manaPoints);
    if (unit.manaPoints > unit.manaPointsMax) {
        Vue.set(unit, 'manaPoints', unit.manaPointsMax);
    }
}
function removeUnitMana(unitId, manaPoints, gameStateUnits) {
    const unit = gameStateUnits[unitId].gameUnit;
    Vue.set(unit, 'manaPoints', unit.manaPoints - manaPoints);
    if (unit.manaPoints < 0) {
        Vue.set(unit, 'manaPoints', 0);
    }
}
