//eslint-disable-next-line
function ownUnit(casterPlayer = {}, unitId) {
    return Boolean(casterPlayer.units && casterPlayer.units[unitId]);
}
function unitSameTeam(casterPlayer = {}, unitId) {
    return Boolean(casterPlayer.team && casterPlayer.team.units && casterPlayer.team.units[unitId]);
}
function playerSameTeam(casterPlayer, targetPlayer) {
    return Boolean(casterPlayer.team && casterPlayer.team.includes(targetPlayer));
}
function verifyUnit(unitId, gameStateUnits) {
    if (!gameStateUnits) {
        throw `Expected @param gameStateUnits but got: ${gameStateUnits}`;
    }
    if (!gameStateUnits[unitId]) {
        throw `Expected gameStateUnit for id: ${unitId}, but got: ${gameStateUnits[unitId]}`;
    }
    return gameStateUnits[unitId];
}
function verifyFriendly(unitId, targetUnitId, gameStateUnits) {
    const unit = verifyUnit(unitId, gameStateUnits);
    const targetUnit = verifyUnit(targetUnitId, gameStateUnits);
    const casterPlayer = unit.player;
    if (!unitSameTeam(casterPlayer, targetUnitId)) {
        return;
    }
    return targetUnit.gameUnit;
}
function verifyEnemy(unitId, targetUnitId, gameStateUnits) {
    const unit = verifyUnit(unitId, gameStateUnits);
    const targetUnit = verifyUnit(targetUnitId, gameStateUnits);
    const casterPlayer = unit.player;
    if (unitSameTeam(casterPlayer, targetUnitId)) {
        return;
    }
    return targetUnit.gameUnit;
}
function removeModifierAferDuration(mod, gameStateUnits, duration, callback = () => {}) {
    const timer = setTimeout(() => {
        const unitId = mod.unitId;
        if (!gameStateUnits[unitId]) {
            console.warn(`abilities.js-
                removeModifierAferDuration:
                    Expected gameStateUnit for id: ${unitId}, but got: ${gameStateUnits[unitId]}
            `);
            return;
        }
        const gameUnit = gameStateUnits[unitId].gameUnit;
        gameUnit.removeModifier(mod);
        callback();
    }, duration);
    return timer;
}
