// https://gist.github.com/NobleUplift/3b64dc4690b7b04bf07b
import utils from '../utils';
import rawUnits from '../unit_data_raw/warcraft_III_stats_JS_EXPORT';

/*eslint-disable quotes*/
function csvToArray(csv) {
    var lines = csv.split("\n");
    var result = [];
    var headers = lines[0].split(",");
    result.push(headers);
    let currentline;
    for (let i = 1; i < lines.length; i++) {
        currentline = lines[i].split(",");
        result.push(currentline);
    }
    return result;
}

const units = csvToArray(rawUnits);
const isNum = {
    Gold: 1,
    Lumber: 1,
    Supply: 1,
    'Hit Points': 1,
    'Hit Points Maximum': 1,
    'Hit Points Regen': 1,
    'Mana Points': 1,
    'Mana Points Regen': 1,
    Armor: 1,
    'Armor Maximum': 1,
    'Day Sight': 1,
    'Night Sight': 1,
    'Speed': 1,
    'Build Time': 1,
    Attack: 1,
    'Ground Attack': 1,
    'Ground Attack Maximum': 1,
    'Second Ground Attack': 1,
    'Second Ground Attack Maximum': 1,
    'Air Attack': 1,
    'Air Attack Maximum': 1,
    Cooldown: 1,
    Range: 1,
    'Range Maximum': 1,
    hidden: 1,
};
// use clone
const headerNames = JSON.parse(JSON.stringify(units[0]));
const headerEnums = {};
headerNames.forEach((enumName, index) => {
    headerEnums[enumName] = index;
    headerEnums[utils.getEnumFromName(enumName)] = index;
    isNum[utils.getEnumFromName(enumName)] = isNum[enumName];
});
// console.log(headerEnums)
units.forEach((row, rowIndex) => {
    row.forEach((value, colIndex) => {
        //convert first row to enum lower snake case
        if (rowIndex === 0) {
            units[rowIndex][colIndex] = utils.getEnumFromName(units[rowIndex][colIndex]);
            return;
        }
        const headerName = headerNames[colIndex];
        if (isNum[headerName]) {
            units[rowIndex][colIndex] = Number(units[rowIndex][colIndex]) || 0;
        } else {
            units[rowIndex][colIndex] = utils.getEnumFromName(units[rowIndex][colIndex]);
        }
    });
});
const headers = units[0];
const humans = units.slice(1, 18);
const orcs = units.slice(18, 37);
const nightelves = units.slice(37, 54);
const undead = units.slice(54, 72);
export default {
    headers,
    headerEnums,
    headerNames,
    humans,
    orcs,
    nightelves,
    undead,
};
