
const DGAB = {
    injectGA(i, s, o, g, r, a, m) {
        i['GoogleAnalyticsObject'] = r;
        i[r] = i[r] || function() {
            //eslint-disable-next-line prefer-rest-params
            (i[r].q = i[r].q || []).push(arguments);
        };
        i[r].l = 1 * new Date();
        //eslint-disable-next-line no-param-reassign
        a = s.createElement(o);
        //eslint-disable-next-line no-param-reassign
        m = s.getElementsByTagName(o)[0];
        a.async = 1;
        a.src = g;
        m.parentNode.insertBefore(a, m);
    },

    sentNotDetected: false,
    gaBlockNotDetected(callback) {
        if (!DGAB.sentNotDetected) {
            //eslint-disable-next-line no-undef
            DGAB.sentNotDetected = true;
        }
        if (typeof callback === 'function') {
            callback();
        }
        return false;
    },

    sentDetected: false,
    gaBlockDetected(callback) {
        if (!DGAB.sentDetected) {
            //eslint-disable-next-line no-undef
            DGAB.sentDetected = true;
        }
        if (typeof callback === 'function') {
            callback();
        }
        return true;
    },

    parseXHR(xhr) {
        if (xhr.status === 0 && xhr.readyState === 0) {
            return DGAB.gaBlockDetected();
        }
        return false;
    },
    //called by main.js
    //eslint-disable-next-line no-unused-vars
    detectGABlock() {
        return $.ajax({
            url: 'https://www.google-analytics.com/debug/collect?tid=fake&v=1',
            method: 'GET'
        })
            .then(DGAB.gaBlockNotDetected)
            .catch(DGAB.parseXHR);
    }
};

export default DGAB;
