<script>
import utils from '../utils';

export default {
    name: 'Volume',
    props: {
        'volumeState': {
            type: Object,
            required: true,
            default: () => ({
                value: 0.5,
                muted: false,
            }),
        },
        'iconColor': {
            type: String,
            required: false,
            default: () => ('white'),
        },
        'trackColor': {
            type: String,
            required: false,
            default: () => ('#9b9b9b'),
        },
        'trackColorHover': {
            type: String,
            required: false,
            default: () => ('gold'),
        }
    },
    components: {},
    data() {
        return {
            userString0: 'mute',
            userString1: 'unmute',
            isIeOrEdge: utils.isEdgeOrIE(),
            volumeProgressStyle: {
                width: `${0.5 * 70}px`,
            },
        };
    },
    computed: {
        volumeValue() {
            const value = this.volumeState.muted ? 0 : this.volumeState.value;
            this.volumeProgressStyle.width = `${70 * value}px`;
            return value;
        },
    },
    methods: {
        updateVolume(e) {
            // We catch both change and input events to handle IE
            // @input gets continuous updates while dragging, which handles most browsers.
            // Not supported for IE.
            // @change is only triggered when when user releases mouse. However, for IE
            // change event provides continous updates similar to input event for other browsers.
            const value = e.currentTarget.valueAsNumber;
            const volume = {
                value: value,
                muted: !(value > 0)
            };
            // this.$store.dispatch('saveVolume', volume);
            this.$emit('saveVolume', volume);
            this.volumeProgressStyle.width = `${70 * volume.value}px`;
        },
        toggleVolume() {
            var volume = {
                value: this.volumeState.value,
                muted: !this.volumeState.muted
            };
            // var volumeValue = volume.muted ? 0 : volume.value;
            // this.$store.dispatch('saveVolume', volume);
            this.$emit('saveVolume', volume);
            // this.$emit('toggleVolume', volumeValue);
        },
    },
};

</script>
<template>
<div class="volume">
    <div class="volume-icon" ref="volumeIcon" @click="toggleVolume">
        <svg v-if="volumeValue > 0"
            class="volume-present"
            v-tooltip="userString0"
            xmlns="http://www.w3.org/2000/svg" version="1.0" width="500" height="500" viewBox="0 0 75 75">
            <path d="M39.389,13.769 L22.235,28.606 L6,28.606 L6,47.699 L21.989,47.699 L39.389,62.75 L39.389,13.769z"
                :stroke="iconColor"
                :fill="iconColor"
                style="stroke-width:5;stroke-linejoin:round;"/>
            <path d="M48,27.6a19.5,19.5 0 0 1 0,21.4M55.1,20.5a30,30 0 0 1 0,35.6M61.6,14a38.8,38.8 0 0 1 0,48.6"
                :stroke="iconColor"
                fill="none"
                style="stroke-width:5;stroke-linecap:round"/>
        </svg>
        <svg v-else
            class="volume-muted"
            v-tooltip="userString1"
            xmlns="http://www.w3.org/2000/svg"
            xml:space="preserve"
            version="1.0"
            id="layer1"
            width="400pt"
            height="400pt"
            viewBox="0 0 75 75">
            <g id="g1">
                <polygon id="polygon1"
                    points="39.389,13.769 22.235,28.606 6,28.606 6,47.699 21.989,47.699 39.389,62.75 39.389,13.769"
                    :fill="iconColor"
                    :stroke="iconColor"
                    style="stroke-width:5;stroke-linejoin:round;"/>
                <path id="path3003"
                    d="M 48.651772,50.269646 69.395223,25.971024"
                    :stroke="iconColor"
                    fill="none"
                    style="stroke-width:5;stroke-linecap:round"
                    inkscape:connector-curvature="0"
                    sodipodi:nodetypes="cc"/>
                <path id="path3003-1"
                    :stroke="iconColor"
                    fill="none"
                    d="M 69.395223,50.269646 48.651772,25.971024"
                    style="stroke-width:5;stroke-linecap:round"
                    inkscape:connector-curvature="0"
                    sodipodi:nodetypes="cc"/>
            </g>
        </svg>
    </div>
    <div class="volume-control" :class="{ 'shift-up': isIeOrEdge }" >
        <span class="volume-progress-track"
            :class="{ 'shift-up': isIeOrEdge }"
            :style="volumeProgressStyle">
        </span>
        <input class="vol-slider"
            step="0.01"
            min="0"
            max="1"
            type="range"
            :value="volumeValue"
            @input="updateVolume"
            @change="updateVolume"/>
    </div>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';

.volume {
    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
    .volume-icon {
        margin-right: 5px;
        .volume-present, .volume-muted {
            width: 25px;
            height: 25px;
            fill: gold;
        }
    }
    .volume-control {
        display: flex;
        width: 70px;
        align-items: center;
        border-radius: 2.5px;
        height: 5px;
        background-color: $grey-d;
        &:active,
        &:hover {
            .volume-progress-track {
                background-color: gold;
            }
        }
        .volume-progress-track {
            border-radius: 2.5px;
            height: 5px;
            z-index: 100;
            position: absolute;
            background-color: $grey-m;
        }
        .volume-progress-track.shift-up {
            margin-top: -2px;
        }
        input[type=range] {
            z-index: 101;
            padding: 5px 0;
            margin: 0;
            background: transparent;
            -webkit-appearance: none;
            &:focus {
                outline: none;
            }
            &::-webkit-slider-runnable-track {
                width: 100%;
                height: 4px;
                cursor: pointer;
                background: transparent;
            }
            &::-webkit-slider-thumb {
                border-radius: 50%;
                width: 10px;
                height: 10px;
                background: transparent;
                &:active,
                &:hover {
                    background-color: white;
                }

                cursor: pointer;
                -webkit-appearance: none;
                margin-top: -3px;
            }
            &::-moz-range-track {
                width: 100%;
                height: 4px;
                cursor: pointer;
                background: transparent;
            }
            &::-moz-range-thumb {
                border: none;
                background: transparent;
                &:active,
                &:hover {
                    background-color: white;
                }

                border-radius: 50%;
                width: 10px;
                height: 10px;
                cursor: pointer;
                margin-top: -3px;
            }
            &::-ms-track {
                width: 100%;
                height: 4px;
                cursor: pointer;
                background: transparent;
                border-color: transparent;
                color: transparent;
                border-width: 6px 0;
            }
            &::-ms-fill-lower {
                background: transparent;
            }
            &::-ms-fill-upper {
                background: transparent;
            }
            &::-ms-thumb {
                background: transparent;
                &:active,
                &:hover {
                    background-color: white;
                }

                border: none;
                border-radius: 50%;
                width: 10px;
                height: 10px;
                cursor: pointer;
            }
            &::-ms-tooltip {
                display: none;
            }
        }
    }
    .volume-control.shift-up {
        border-top: 1px solid $grey-d;
    }
}

</style>
