import unitAttributes from '../unitAttributes';
import unitAbilities from '../units/unitAbilities';
import unitsIndex from './index';

/*eslint-disable no-underscore-dangle*/
// const second = 1000;

class GameUnit {
    constructor(gameId) {
        const info = gameId.split('_');
        const err = new Error('GameUnit.constructor');
        if (info[1] === undefined) {
            err.message = `Invalid param @gameId: ${gameId}`;
            throw err;
        }
        this.gameId = gameId;
        const [race, index] = info;
        this.race = race;
        this.index = index;
        this.alive = true;
        this.abilitiesInfo = unitAbilities[gameId];
        this.autocastStatus = {};
        this.coolDowns = {};
        this.autoAttackMods = [];
        Object.keys(this.abilitiesInfo.abilities).forEach((abilityName) => {
            const ability = this.abilitiesInfo.abilities[abilityName];
            if (ability.autocast) {
                this.autocastStatus[abilityName] = Boolean(ability.defaultStatus);
            }
            if (ability.autoAttackMod) {
                ability.name = abilityName;
                this.autoAttackMods.push(ability);
            }
            // if (ability.passive && ability.call) {
            //     ability.call()
            // }
        });
        this.hitPoints = unitsIndex.getUnitAttribute(race, index, 'hit_points');
        this.hitPointsMax = unitsIndex.getUnitAttribute(race, index, 'hit_points');
        this.manaPoints = unitsIndex.getUnitAttribute(race, index, 'mana_points');
        this.manaPointsMax = unitsIndex.getUnitAttribute(race, index, 'mana_points');
        this.groundAttackType = unitsIndex.getUnitAttribute(race, index, 'ground_attack_type');
        this.secondGroundAttackType = unitsIndex.getUnitAttribute(race, index, 'second_ground_attack_type');
        this.defaultPosition = unitsIndex.getUnitAttribute(race, index, 'default_position');
        this.groundAttackLevel = unitsIndex.getUnitAttribute(race, index, 'ground_attack');
        this.groundAttackLevelMax = unitsIndex.getUnitAttribute(race, index, 'ground_attack_maximum');
        this.secondGroundAttackLevel = unitsIndex.getUnitAttribute(race, index, 'second_ground_attack');
        this.secondGroundAttackLevelMax = unitsIndex.getUnitAttribute(race, index, 'second_ground_attack_maximum');
        this.airAttackLevel = unitsIndex.getUnitAttribute(race, index, 'air_attack');
        this.airAttackLevelMax = unitsIndex.getUnitAttribute(race, index, 'air_attack_maximum');
        this.airAttackType = unitsIndex.getUnitAttribute(race, index, 'air_attack_type');
        this.armorType = unitsIndex.getUnitAttribute(race, index, 'armor_type');
        this.armorLevel = unitsIndex.getUnitAttribute(race, index, 'armor');
        this.range = unitsIndex.getUnitAttribute(race, index, 'range');
        this._damageTypeModifiers = unitAttributes.getDefaultDamageTypeModifiers(); // ie this unit takes less peircing damage
        this._movementSpeedModifiers = [];
        this._attackSpeedModifiers = [];
        this._attackLevelModifiers = [];
        this._armorLevelModifiers = [];
        this._allMods = [];
        this.groundAttackEnum = unitAttributes.attack_enum[this.groundAttackType];
        this.secondGroundAttackEnum = unitAttributes.attack_enum[this.secondGroundAttackType];
        this.airAttackEnum = unitAttributes.attack_enum[this.airAttackType];
        this.armorEnum = unitAttributes.armor_enum[this.armorType];
        this.armorReduction = unitAttributes.armor_reduction(this.armorLevel);
        this.hitPointsRegen = unitsIndex.getUnitAttribute(race, index, 'hit_points_regen');
        this.manaPointsRegen = unitsIndex.getUnitAttribute(race, index, 'mana_points_regen');
        this.actionCount = {
            yes: 1,
            yesAttack: 1,
            what: 1,
            pissed: 1,
        };
        this.previousAction = '';
        this.autoAttackTarget = '';
        this.abilityTarget = '';
        this.tempSrc = ''; // used for temporary effects such as polymorph
        this.disabled = false;
        // spells that effect this stat replace one another; it's a scaler
        this.damageTakenMultiplier = 1;
    }
    // getters
    getDamageTypeModifier(attackType) {
        return unitAttributes.getDamageTypeModifier(this, attackType);
    }
    getAttributeModifier(attributeType) {
        return unitAttributes.getAttributeModifier(this, attributeType);
    }
    getAllModifiers() {
        return unitAttributes.getAllModifiers(this);
    }
    // setters
    /* addAttributeModifier @param mod:
    *    { attributeType, percentage = 0, value = 0, operator = '*', name, modType, good, unitId }
    */
    addAttributeModifier(mod) {
        const gameUnit = this;
        mod.gameUnit = gameUnit;
        return unitAttributes.addAttributeModifier(mod);
    }
    /* addDamageTypeModifier @param mod:
    *    { attackType, percentage = 0, value = 0, operator = '*', name, modType, good, unitId }
    */
    addDamageTypeModifier(mod) {
        const gameUnit = this;
        mod.gameUnit = gameUnit;
        return unitAttributes.addDamageTypeModifier(this, mod);
    }
    // removers
    removeModifier(mod) {
        return unitAttributes.removeModifier(this, mod);
    }
    //eslint-disable-next-line
    incrementAttackActionResponse() {
        // const action = 'yesAttack';
    }
    // _hpRegenTimerCallback () {
    //     this.addUnitHealth(this.id, this.hitPointsRegen);
    // }
    // _mpRegenTimerCallback () {
    //     this.addUnitMana(this.id, this.manaPointsRegen);
    // }
    // setRegenInterval() {
    //     this._regenTimer = setInterval(() => {
    //         this._hpRegenTimerCallback();
    //         this._mpRegenTimerCallback();
    //     }, second);
    // }
}

export default GameUnit;
