<script>

import '../stylesheets/app.scss';
import constants from '../constants';
import utils from '../utils';
import store from '../store';
import BattleView from './BattleView';
import EntryMenu from './EntryMenu';
import SinglePlayerMenu from './SinglePlayerMenu';
import MultiPlayerMenu from './MultiPlayerMenu';
import CreditMenu from './CreditMenu';
import Volume from './Volume';
import unitsIndex from '../units';

export default {
    components: {
        EntryMenu,
        SinglePlayerMenu,
        MultiPlayerMenu,
        BattleView,
        CreditMenu,
        Volume,
    },
    data() {
        return {
            store,
            isIncognito: false,
            darkNavyBlue: '#000b1c',
            currentView: 'EntryMenu',
            userString0: 'Login',
            showEntryMenu: true,
            hasCurrentView: true,
            user: {},
            battleMatch: null,
            username: null,
        };
    },
    computed: {
        appStyle() {
            return { cursor: `url(static/imgs/${this.selectedRace}.cur), auto` };
        },
        selectedRace() {
            return this.$store.state.userSession.user.race || 'Human';
        },
        currentMusicSrc() {
            return this.$store.state.volume.music.src;
        },
    },
    watch: {
        currentMusicSrc(newVal, oldVal) {
            if (newVal === oldVal) {
                return;
            }
            this.$refs.musicVolume.load();
            this.$refs.musicVolume.play();
        }
    },
    created() {
        this.$store.commit('setUsername', undefined);
        this.username = this.$store.state.userSession.username;
        this.easterEgg();
        this.popUpPlayer = this.$getSoundPlayer('static/sounds/RightGlueScreenPopUpTrim.wav');
        this.popDownPlayer = this.$getSoundPlayer('static/sounds/RightGlueScreenPopDownTrim.wav');
        this.isIncognito = utils.detectIncognitoMode();
        const self = this;
        this.$trackWhenLoad(() => {
            self.$Tracking.pageViewHit(`/${self.currentView}`);
            self.$Tracking.appLoad(`/${self.currentView}`);
            if (self.isIncognito) {
                self.$Tracking.incognitioMode();
            }
        });
    },
    mounted() {
        this.$refs.musicVolume.volume = 0.2;
    },
    methods: {
        showSinglePlayerMenu() {
            this.navigateMenu('SinglePlayerMenu');
        },
        showMultiPlayerMenu() {
            this.navigateMenu('MultiPlayerMenu');
        },
        showCreditsMenu() {
            this.navigateMenu('CreditMenu');
        },
        navigateBack() {
            this.navigateMenu(this.previousMenu);
        },
        navigateMenu(menuComponent) {
            this.previousMenu = this.currentView;
            this.hasCurrentView = false;
            const self = this;
            return this.soundPopUp().then(() => {
                self.hasCurrentView = true;
                self.currentView = menuComponent;
                self.soundPopDown();
            });
        },
        gameEnd(gameResults) {
            const results = JSON.parse(gameResults);
            const { didWin, race, matchMode } = results;
            this.$Tracking.gameEnd(race, matchMode, didWin);
            this.battleMatch = null;
            this.navigateBack();
        },
        soundPopUp() {
            const player = this.popUpPlayer;
            const duration = player.duration;
            return new Promise((resolve, reject) => {
                // self.$playSound('static/sounds/RightGlueScreenPopUpTrim.wav').then(resolve).catch(reject);
                setTimeout(resolve, duration * 0.5 * 1000);
                player.addEventListener('error', reject);
                player.play();
            });
        },
        soundPopDown() {
            const player = this.popDownPlayer;
            const duration = player.duration;
            return new Promise((resolve, reject) => {
                // self.$playSound('static/sounds/RightGlueScreenPopDownTrim.wav').then(resolve).catch(reject);
                setTimeout(resolve, duration * 0.6 * 1000);
                player.addEventListener('error', reject);
                player.play();
            });
        },
        easterEgg() {
            //eslint-diable-next-line max-len
            const colorStr = 'color: #4f92ff';
            const string = constants['EASTER_EGG'];
            console.log(`%c ${string}`, `font-weig!ht: bold; font-size: 14px; ${colorStr}; background-color: #333333`);
            console.log(constants['HELP_US_GROW']);
        },
        // startGame(jsonGameInfo) {
        //     const gameInfo = JSON.parse(jsonGameInfo);

        // },
        startSinglePlayer(gameInfo) {
            /*
            * A game consists of teams, players, units and buildings.
            * Before starting a match, the number of teams must be known.
            * The players of each team must also be known. The game mode
            * selected then determines the number of starting units as well
            * as what race.
            */
            const unitCount = gameInfo.unitCount;
            // fill in units
            this.$store.getters.playerIds.forEach((id) => {
                const player = this.$store.state.gameState.players[id];
                const diff = unitCount - player.units.length;
                if (player.race === 'Random' || player.race === 4) {
                    this.$store.commit('updatePlayerRace', { id, race: unitsIndex.getRandomRace() });
                }
                for (let k = 0; k < diff; k++) {
                    player.startingUnits.push(unitsIndex.getRandomUnitId(player.race));
                }
                player.startingUnits.forEach((unitId) => {
                    this.$store.dispatch('addUnit', { playerId: id, unitId });
                });
            });
            // random unit versus
            if (gameInfo.mode === 0) {
                // (Player) team 1 v (AI) team(s)
            }
            this.battleMatch = true;
            this.hasCurrentView = false;
            this.soundPopUp();
            // const self = this;
            // const race = unitsIndex.races[userPlayer.race] || userPlayer.race;
            // this.$trackWhenLoad(() => {
            //     self.$Tracking.gameStart(race, gameInfo.mode, unitCount);
            // });
        },
        saveMusicVolume({ value, muted }) {
            this.$store.commit('saveVolume', { volumeType: 'music', value, muted });
            this.$refs.musicVolume.volume = value;
            if (muted) {
                this.$refs.musicVolume.volume = 0;
            }
        },
    },
};

</script>
<template>
<div id="app" :style="appStyle">
    <Volume id="global-volume"
      :volumeState="$store.state.volume.music"
      @saveVolume="saveMusicVolume">
    </Volume>
    <transition name="lift">
        <component :is="currentView" v-if="hasCurrentView"
            :chainBackgroundColor="darkNavyBlue"
            :username="username"
            @back="navigateBack"
            @showSinglePlayerMenu="showSinglePlayerMenu"
            @showMultiPlayerMenu="showMultiPlayerMenu"
            @showCreditsMenu="showCreditsMenu"
            @startSinglePlayer="startSinglePlayer">
        </component>
    </transition>
    <transition name="fade">
        <BattleView class="battle-view" v-if="battleMatch"
            :teams="$store.getters.teamIds.map((id) => $store.state.gameState.teams[id])"
            :players="$store.getters.playerIds.map((id) => $store.state.gameState.players[id])"
            :units="$store.getters.unitIds.map((id) => $store.state.gameState.units[id])"
            :user="$store.state.userSession.user"
            @gameEnd="gameEnd">
        </BattleView>
    </transition>
    <div class="foglayer_01 fog">
        <div class="image01"></div>
        <div class="image02"></div>
    </div>
    <div class="foglayer_02 fog">
        <div class="image01"></div>
        <div class="image02"></div>
    </div>
    <div class="foglayer_03 fog">
        <div class="image01"></div>
        <div class="image02"></div>
    </div>
    <audio ref="musicVolume"
        id="entry-page-audio"
        autoplay="true"
        loop="true"
        display="none">
      <source ref="musicVolumeSource" :src="currentMusicSrc" type="audio/mp3">
    </audio>
    <iframe src="static/sounds/silence.mp3" allow="autoplay" id="silence-audio" style="display:none"></iframe>
</div>
</template>

