<script>
const colWidth = '35px';
export default {
    name: 'menu-frame',
    props: [],
    data() {
        return {
            colWidth,
            horizontalRivets: 8,
            verticalRivets: 8,
            top: {
                width: '100%',
                height: colWidth,
            },
            bottom: {
                width: '100%',
                height: colWidth,
            },
            left: {
                width: colWidth,
                height: '100%',
            },
            right: {
                width: colWidth,
                height: '100%',
            },
        };
    },
    computed: {},
    methods: {},
};

</script>
<template>
<div class="menu-frame">
    <div class="top metal-col" :style="top">
        <div v-for="n in horizontalRivets"
            class="rivet"
            :class="{ 'frost-shadow': !!(n % 3), 'frost-shadow-2': !(n % 3) }"
            :key="n">
        </div>
    </div>
    <div class="inner ">
        <div class="left metal-col" :style="left">
            <div v-for="n in verticalRivets"
                class="rivet"
                :class="{ 'frost-shadow': !!(n % 3), 'frost-shadow-2': !(n % 3) }"
                :key="n">
            </div>
        </div>
        <div class="slot-container">
            <slot class="slot"></slot>
        </div>
        <div class="right metal-col" :style="right">
            <div v-for="n in verticalRivets"
                class="rivet"
                :class="{ 'frost-shadow': !!(n % 3), 'frost-shadow-2': !(n % 3) }"
                :key="n">
            </div>
        </div>
    </div>
    <div class="bottom metal-col" :style="bottom">
        <div v-for="n in horizontalRivets"
            class="rivet"
            :class="{ 'frost-shadow': !!(n % 3), 'frost-shadow-2': !(n % 3) }"
            :key="n">
        </div>
    </div>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';
@import '../stylesheets/ice-age';
@import '../stylesheets/metal-decor';

$width: 100%;
$height: 100%;

.menu-frame {
    display: flex;
    position: absolute;
    z-index: 1;
    flex-direction: column;
    background-color: rgba(0,0,0,0.3);
    .top, .bottom, .left, .right {
        align-items: center;
        display: flex;
        justify-content: space-around;
    }
    .top, .bottom {
        flex-direction: row;
    }
    .inner {
        display:flex;
        flex-grow: 2;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        .slot-container {
            width: 100%;
            height: 100%;
            display: inline-block;
            font-weight: 100;
            font-family: 'Times New Roman';
            &:hover {
                color: $dark-navy-blue;
            }
        }
    }
}

</style>
