<script>

export default {
    name: 'inventory',
    props: {
        'items': {
            type: Array,
            required: false,
            default: () => ([]),
        },
        'maxSlots': {
            type: Number,
            required: false,
            default: () => (9),
        },
    },
    components: {},
    data() {
        return {
            userMessage0: 'Inventory',
        };
    },
    computed: {
        openSlots() {
            return this.maxSlots - this.items.length;
        },
    },
    methods: {
        itemClick(index) {
            this.$emit('click', index);
        }
    },
};

</script>
<template>
<div class="inventory">
    <div class="header">
        <div class="top"></div>
        <div class="body">
            <div class="left"></div>
            <div class="center">
                {{ userMessage0 }}
            </div>
            <div class="right"></div>
        </div>
        <div class="bot"></div>
    </div>
    <div class="slots-container">
        <div v-for="index in openSlots"
            :key="index"
            class="slot-container"
            @click="itemClick(index)">
            <div class="slot"></div>
        </div>
    </div>
</div>
</template>
