<script>
import menuFrame from './menuFrame';
import menuChain from './menuChain';
import woodFrame from './woodFrame';

export default {
    name: 'CreditMenu',
    components: {
        'menu-frame': menuFrame,
        'menu-chain': menuChain,
        'wood-frame': woodFrame,

    },
    props: ['chainBackgroundColor'],
    data() {
        return {
            blizzard: 'https://en.wikipedia.org/wiki/Blizzard_Entertainment',
            warcraft3Original: 'http://classic.battle.net/war3/',
            userString0: 'Back',
            userString1: 'Blizzard Entertainment',
            userString2: 'And Me, obviously.',
            userString3: 'Information about the original Warcraft III game',
        };
    },
    computed: {},
    methods: {},
    created() {
        this.$Tracking.viewCredits();
    },
};

</script>
<template>
<div id="credit-menu">
    <wood-frame id="test" :width="420" :height="180" v-if="false">
        <p id="test-p"> Victory! </p>
    </wood-frame>
    <div id="menu-chains-container">
        <menu-chain :backgroundColor="chainBackgroundColor"></menu-chain>
        <div class="spacer"></div>
        <menu-chain :backgroundColor="chainBackgroundColor"></menu-chain>
    </div>
    <menu-frame id="credit-menu-container">
        <a class="wc3-btn credit" :href="blizzard">
            {{userString1}}
        </a>
        <div class="wc3-btn credit">
            {{userString2}}
        </div>
        <a class="wc3-btn credit" :href="warcraft3Original">
            {{userString3}}
        </a>
        <div id="back" class="wc3-btn credit"
            @click="$emit('back')">
            {{userString0}}
        </div>
    </menu-frame>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';

#test {
    margin-top: 20px;
    z-index: 100;
    position: relative;
    width: 420px;
    height: 180px;
}
#test-p {
    width: 420px;
    height: 180px;
    color: white;
    justify-content: center;
    display: flex;
}

$chain-height: 196px;

#credit-menu {
    height: 100%;
    min-height: 300px;
    min-width: 800px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition: all 0.7s linear;
    transition-duration: 0.7s;
    #menu-chains-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: absolute;
        z-index: 0;
        height: $chain-height;
        .spacer {
            width: 294px;
        }
    }
    #credit-menu-container {
        margin-top: $chain-height;
        height: 660px;
        width: 800px;
        .credit {
            margin-left: auto;
            margin-right: auto;
            display: flex;
            justify-content: center;
            width: 500px;
            height: 100px;
        }
    }
}

</style>
