<script>

import menuFrame from './menuFrame';
import menuChain from './menuChain';
import DropDown from './DropDown';
import Team from './Team';
import constants from '../constants';

export default {
    name: 'SinglePlayer',
    components: {
        'menu-frame': menuFrame,
        'menu-chain': menuChain,
        DropDown,
        Team,
    },
    props: ['chainBackgroundColor', 'username'],
    data() {
        return {
            userSession: this.$store.state.userSession,
            gameState: this.$store.state.gameState,
            gameModeSelection: ['Random Units', 'Campaign', 'Custom'],
            randomUnitCountSelection: ['3 v 3', '5 v 5', '7 v 7', '10 v 10', '12 v 12'],
            unitCount: [3, 5, 7, 10, 12],
            raceSelection: constants.RACE_SELECTION,
            playerRace: 0,
            opponentRaceIndex: 4,
            selectedGameMode: 0,
            selectedUnitCountIndex: 1,
            currentUsername: '',
            userString0: 'Unit Count',
            userString1: 'Random',
            userString2: 'Human',
            userString3: 'Orc',
            userString4: 'Undead',
            userString5: 'Night Elf',
            userString6: 'Player Race',
            userString7: 'Game Mode',
            userString8: 'Back',
            userString9: 'Start',
            userString10: 'Opponent Race',
            userString11: 'username',
            selectStyle: {
                width: '200px',
                height: '35px',
                'font-family': 'Times New Roman',
                'font-weight': '100',
                'font-size': '25px',
            },
            opponentTid: '',
            opponentPid: '',
        };
    },
    computed: {
        randomUnitMode() {
            return this.selectedGameMode === 0;
        },
        campaignMode() {
            return this.selectedGameMode === 1;
        },
        customMode() {
            return this.selectedGameMode === 2;
        },
        unitCountVal() {
            return this.unitCount[this.selectedUnitCountIndex];
        },
        teams() {
            return Object.keys(this.gameState.teams)
                .filter(teamId => teamId !== 'length')
                .map(teamId => this.gameState.teams[teamId]);
        },
    },
    watch: {
        currentUsername(val) {
            if (val && val !== 'Player 1') {
                this.$Tracking.setUsername(val);
            }
            this.$store.commit('setUsername', val);
        },
        async randomUnitMode(newVal) {
            if (!newVal) {
                return;
            }
            /*
            * Consists of
            *   - 2 teams (1v1)
            *   - N number of units set per team/player
            *   - Each player has a selected or set race
            *   - Player game mode, eliminate all teams units
            *   - Team game mode, all players must lose
            */
            const tid = await this.$store.dispatch('addTeam', { name: undefined });
            this.$store.dispatch('addPlayer', { name: undefined, teamId: tid });
        },
        async customMode(newVal) {
            if (!newVal) {
                return;
            }
            const tid = await this.$store.dispatch('addTeam', { name: undefined });
            this.opponentTid = tid;
            const pid = await this.$store.dispatch('addPlayer', { name: 'Computer', teamId: tid });
            this.opponentPid = pid;
            this.$store.commit('setTeamMaxPlayers', { id: this.userSession.teamId, max: 2 });
            this.$store.commit('setTeamMaxPlayers', { id: tid, max: 2 });
        }
    },
    async created() {
        // single player menu vue component currently functions as the initial game lobby
        // for human player that starts browser session
        const teamId = await this.$store.dispatch('addTeam', { name: undefined });
        const userId = await this.$store.dispatch('addPlayer', { user: true, name: this.userSession.username, teamId, race: 'Human' });
        await this.$store.dispatch('setUser', { id: userId });
        this.$store.commit('setHostPlayerId', { id: userId });
        //set up random team mode
        const tid = await this.$store.dispatch('addTeam', { name: undefined });
        this.opponentTid = tid;
        const pid = await this.$store.dispatch('addPlayer', { name: 'Computer', teamId: tid, race: this.raceSelection[this.opponentRaceIndex] });
        this.opponentPid = pid;
        this.$Tracking.viewSinglePlayer();
    },
    methods: {
        async selectGameMode(index) {
            this.selectedGameMode = Number(index);
            //clear out all previous game state
            const keep = {};
            keep[this.userSession.teamId] = true;
            // remove all teams except for user player team
            await this.$store.dispatch('removeAllTeams', { keep });
            // remove all previously existing units from user player
            await this.$store.dispatch('removeAllUnitsFromPlayer', { id: this.userSession.userId });
        },
        async createTeam() {
            await this.$store.dispatch('addTeam', { name: undefined });
        },
        async addPlayerAI(teamId) {
            await this.$store.dispatch('addPlayer', { name: 'Computer', teamId });
        },
        selectUnitCount(index) {
            this.selectedUnitCountIndex = index;
        },
        selectPlayerRace(index) {
            this.playerRace = Number(index);
            // foward race selection to change cursor type
            this.$store.commit('setUserRace', this.raceSelection[this.playerRace]);
        },
        selectOpponentRaceIndex(index) {
            this.opponentRaceIndex = Number(index);
            this.$store.commit('updatePlayerRace', { id: this.opponentPid, race: this.raceSelection[this.opponentRaceIndex] });
        },
        async closeTeam(id) {
            await this.$store.dispatch('removeAllPlayersFromTeam', { id });
            this.$store.commit('removeTeam', { id });
        },
        start() {
            /*
            * A game consists of teams, players, units and buildings.
            * Before starting a match, the number of teams must be known.
            * The players of each team must also be known. The game mode
            * selected then determines the number of starting units as well
            * as what race.
            */
            const game = {};
            game.mode = this.selectedGameMode;
            if (game.mode === 0) {
                game.unitCount = this.unitCountVal;
            } else {
                game.unitCount = 7;
            }
            this.$emit('startSinglePlayer', game);
        }
    },
};

</script>
<template>
<div id="single-player-menu">
    <div id="menu-chains-container">
        <menu-chain
            :backgroundColor="chainBackgroundColor">
        </menu-chain>
        <div class="spacer"></div>
        <menu-chain
            :backgroundColor="chainBackgroundColor">
        </menu-chain>
    </div>
    <menu-frame id="single-player-menu-container">
        <div id="username">
            <label
                id="username-label"
                for="username">
                {{ userString11 }}
            </label>
            <input
                id="username-input"
                v-model="currentUsername"
                name="username"
                type="text"
                :placeholder="username"
                @paste.stop
                @keydown.stop>
        </div>
        <DropDown
            id="game-mode"
            class="menu"
            :options="gameModeSelection"
            :title="userString7"
            :default="selectedGameMode"
            :selectStyle="selectStyle"
            @select="selectGameMode">
        </DropDown>
        <div
            v-if="randomUnitMode"
            id="random-unit-mode">
            <DropDown
                id="unit-count"
                class="menu"
                :options="randomUnitCountSelection"
                :title="userString0"
                :default="selectedUnitCountIndex"
                :selectStyle="selectStyle"
                @select="selectUnitCount">
            </DropDown>
            <DropDown
                id="player-race"
                class="menu"
                :options="raceSelection"
                :title="userString6"
                :default="playerRace"
                :selectStyle="selectStyle"
                @select="selectPlayerRace">
            </DropDown>
            <DropDown
                id="opponent-race"
                class="menu"
                :options="raceSelection"
                :title="userString10"
                :default="opponentRaceIndex"
                :selectStyle="selectStyle"
                @select="selectOpponentRaceIndex">
            </DropDown>
        </div>
        <div
            v-if="customMode"
            id="custom-mode">
            <Team
                v-for="(team, teamId) in teams"
                :key="teamId"
                :team="team"
                @closeTeam="closeTeam">
            </Team>
        </div>
        <div
            id="start"
            class="wc3-btn"
            @click="start">
            {{ userString9 }}
        </div>
        <div
            id="back"
            class="wc3-btn"
            @click="$emit('back')">
            {{ userString8 }}
        </div>
    </menu-frame>
</div>
</template>
