<script>

import constants from '../constants';
import unitAttributes from '../unitAttributes';

export default {
    name: 'UnitInfoStatus',
    props: {
        'gameUnit': {
            type: Object,
            required: false,
            default: () => ({}),
        },
        'name': {
            type: String,
            required: true,
            default: () => (''),
        },
        'attackType': {
            type: String,
            required: true,
            default: () => (''),
        },
        'secondGroundAttackType': {
            type: String,
            required: true,
            default: () => (''),
        },
        'airAttackType': {
            type: String,
            required: true,
            default: () => (''),
        },
        'armorType': {
            type: String,
            required: true,
            default: () => (''),
        },
        'groundAttack': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'groundAttackMaximum': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'secondGroundAttack': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'secondGroundAttackMaximum': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'airAttack': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'airAttackMaximum': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'armor': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'bonusGroundAttack': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'bonusSecondGroundAttack': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'bonusAirAttack': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'bonusArmor': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'allMods': {
            type: Array,
            required: false,
            default: () => ([]),
        },
    },
    components: {},
    data() {
        return {
            userString0: 'Status: ',
        };
    },
    computed: {
        attackInfoIcon() {
            return `url("static/imgs/Infocard-neutral-attack-${this.attackType}.png")`;
        },
        secondGroundAttackInfoIcon() {
            return `url("static/imgs/Infocard-neutral-attack-${this.secondGroundAttackType}.png")`;
        },
        airAttackInfoIcon() {
            return `url("static/imgs/Infocard-neutral-attack-${this.airAttackType}.png")`;
        },
        armorInfoIcon() {
            return `url("static/imgs/Infocard-neutral-armor-${this.armorType}.png")`;
        },
        damageReductionDescription() {
            return `${Math.round(100 * unitAttributes.armor_reduction(this.armor + this.bonusArmor))}% damage reduction`;
        },
        uniqueAllMods() {
            const seen = {};
            return this.allMods.filter((mod) => {
                if (seen[mod.name] === undefined) {
                    seen[mod.name] = 1;
                    return true;
                }
                return false;
            });
        },
        showSecondGroundAttack() {
            return this.secondGroundAttack && (this.secondGroundAttackType !== this.attackType && this.secondGroundAttack !== this.groundAttack);
        }
    },
    methods: {
        getAttackDescription(type) {
            return constants.ATTACK_TYPE_DESCRIPTIONS[type];
        },
        getArmorDescription(type) {
            return constants.ARMOR_TYPE_DESCRIPTIONS[type];
        },
        getModSrc(mod) {
            return `static/imgs/BTN${mod.name}.png`;
        },
    },
};

</script>
<template>
<div id="menu-info-display">
    <div id="name">{{name}}</div>
    <div id="unit-status">
        <div id="damage-container">
            <div class="damage" v-if="groundAttack">
                <div v-tooltip="getAttackDescription(attackType)"
                    class="attack-type-info"
                    :style="{
                        'background-image': attackInfoIcon,
                    }">
                </div>
                <div id="ground-attack"
                    class="stat">
                    <span class="stat-label"> Damage: </span>
                    <span class="value"> {{ groundAttack }} - {{ groundAttackMaximum }}
                        <span v-if="bonusGroundAttack" class="bonus">
                            + {{ bonusGroundAttack }}
                        </span>
                    </span>
                </div>
            </div>
            <div class="damage" v-if="showSecondGroundAttack">
                <div
                    v-tooltip="getAttackDescription(secondGroundAttackType)"
                    class="attack-type-info"
                    :style="{
                        'background-image': secondGroundAttackInfoIcon,
                    }">
                </div>
                <div
                    id="second-ground-attack"
                    class="stat">
                    <span class="stat-label"> Building Damage: </span>
                    <span class="value"> {{ secondGroundAttack }} - {{ secondGroundAttackMaximum }}
                        <span v-if="bonusSecondGroundAttack" class="bonus">
                            + {{ bonusSecondGroundAttack }}
                        </span>
                    </span>
                </div>
            </div>
            <div class="damage" v-if="airAttack && airAttackType !== attackType">
                <div
                    v-tooltip="getAttackDescription(airAttackType)"
                    class="attack-type-info"
                    :style="{
                        'background-image': airAttackInfoIcon,
                    }">
                </div>
                <div
                    id="air-attack"
                    class="stat">
                    <span class="stat-label"> Air Damage: </span>
                    <span class="value"> {{ airAttack }} - {{ airAttackMaximum }}
                        <span v-if="bonusAirAttack" class="bonus">
                            + {{ bonusAirAttack }}
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <div id="armor-container">
            <div class="armor">
                <div
                    v-tooltip="getArmorDescription(armorType)"
                    class="armor-type-info"
                    :style="{
                        'background-image': armorInfoIcon,
                    }">
                </div>
                <div v-tooltip="damageReductionDescription"
                    class="stat">
                    <span class="stat-label"> Armor: </span>
                    <span class="value">
                        {{ armor }}
                        <span v-if="bonusArmor" class="bonus">
                            + {{ bonusArmor }}
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <div id="status-effects-container">
            <div id="status-effects">
                <span id="status-header"> {{ userString0 }} </span>
                <div v-for="mod in uniqueAllMods"
                    v-tooltip="mod.name"
                    :key="mod.name"
                    class="status-icon">
                    <img :src="getModSrc(mod)">
                </div>
            </div>
        </div>
    </div>
</div>
</template>
<style lang="scss">
