    border-image: url("https://arcman7.github.io/games/ice-age/static/imgs/human_menu/units-name-back.gif") 15 round;
    border-top: 15px solid transparent;
    display: flex;
    flex-direction: column;
    background-color: black;
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 375px;
    height: 100%;
    color: white;
    #name {
        font-size: 20px;
        padding: 3px;
        width: 100%;
        display: flex;
        justify-content: center;
    }
    #unit-status {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-left: 15px;
        margin-top: 10px;
        #damage-container, #armor-container {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: 10px;
            .damage, .armor {
                display: flex;
                margin-left: 6px;
                .attack-type-info, .armor-type-info {
                    width: 55px;
                    height: 55px;
                    margin-right: 10px;
                }
                .stat {
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    letter-spacing: 0.5px;
                    padding: 3px;
                    .stat-label {
                        color: gold;
                    }
                    .value {
                        padding-left: 10px;
                        .bonus {
                            margin-left: 4px;
                            color: #03fc5a;
                            font-weight: bold;
                        }
                    }
                }
            }
        }
        #status-effects-container {
            display: flex;
            justify-content: center;
            #status-effects {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                #status-header {
                    font-size: 20px;
                    margin-right: 10px;
                }
                .status-icon {
                    margin-left: 3px;
                    height: 25px;
                    width: 25px;
                    img {
                        width: 100%;
                        height: 100%;
                    }
                }
            }
        }
    }
}
</style>
