<script>
import DropDown from './DropDown';

export default {
    name: 'LobbySlot',
    components: {
        'DropDown': DropDown,
    },
    props: ['statusIndex', 'index'],
    data() {
        return {
            userString0: 'Open',
            userString1: 'Closed',
            userString2: 'Computer',
            userString3: 'Reserved',
        };
    },
    computed: {
        statusOptions() {
            return [this.userString0, this.userString1, this.userString2, this.userString3];
        },
        isHost() {
            return this.$store.getters.isHost;
        },
    },
    methods: {
        setStatus(statusIndex) {
            this.$emit('hostLobbySlotSelection', { statusIndex, index: this.index });
        },
    },
};

</script>
<template>
<div class="lobby-slot">
    <DropDown
        v-if="isHost"
        :options="statusOptions"
        :default="0"
        :hideLabel="true"
        :selectStyle="{
            width: '200px',
        }"
        class="status"
        @select="setStatus">
    </DropDown>
    <div
        v-else
        class="status">
        {{ statusOptions[statusIndex] }}
    </div>
</div>
</template>
