Object.keys(consts.GAME_MODES.PLAYER).forEach((mode) => {
    let num;
    if (mode.match('UNIT')) {
        num = consts.GAME_MODES.PLAYER[mode];
        consts.GAME_MODES.UNIT_MODES[num] = true;
    }
    if (mode.match('BUILDING')) {
        num = consts.GAME_MODES.PLAYER[mode];
        consts.GAME_MODES.BUILDING_MODES[num] = true;
    }
    if (mode.match('SPECIAL')) {
        num = consts.GAME_MODES.PLAYER[mode];
        consts.GAME_MODES.SPECIAL_PLAYER_MODES[num] = true;
        if (mode.match('BUILDING')) {
            consts.GAME_MODES.SPECIAL_BUILDING_MODES[num] = true;
        }
        if (mode.match('UNIT')) {
            consts.GAME_MODES.SPECIAL_UNIT_MODES[num] = true;
        }
    }
});
Object.keys(consts.GAME_MODES.TEAM).forEach((mode) => {
    if (mode.match('SPECIAL')) {
        const num = consts.GAME_MODES.TEAM[mode];
        consts.GAME_MODES.SPECIAL_TEAM_MODES[num] = true;
    }
});

export default consts;
