const utils = {
    getRandomInt(max, floor = 0) {
        return Math.floor(Math.random() * Math.floor(max)) + floor;
    },
    getRangeBetween(min, max) {
        var list = [];
        for (let i = min; i <= max; i++) {
            list.push(i);
        }
        return list;
    },
    upperCaseAll(string) {
        let str = '';
        for (let i = 0; i < string.length; i++) {
            str += string[i].toLocaleUpperCase();
        }
        return str;
    },
    upperCaseFirstChar(string) {
        return string.charAt(0).toLocaleUpperCase() + string.slice(1);
    },
    upperCaseFirstCharWords(string) {
        let words = string.split(' ');
        words = words.map(word => utils.upperCaseFirstChar(word));
        return words.join(' ');
    },
    lowerCaseAll(string) {
        let str = '';
        for (let i = 0; i < string.length; i++) {
            str += string[i].toLocaleLowerCase();
        }
        return str;
    },
    lowerCaseFirstChar(string) {
        return string.charAt(0).toLocaleLowerCase() + string.slice(1);
    },
    lowerCaseFirstCharWords(string) {
        let words = string.split(' ');
        words = words.map(word => utils.lowerCaseFirstChar(word));
        return words.join(' ');
    },
    getEnumFromName(string) {
        return utils.lowerCaseFirstCharWords(string).replace(/\s/g, '_');
    },
    getNameFromEnum(string) {
        return utils.upperCaseFirstCharWords(string.replace(/_/g, ' '));
    },
    getAbsoluteBox(domElement) {
        var top = 0;
        var left = 0;
        do {
            top += domElement.offsetTop || 0;
            left += domElement.offsetLeft || 0;
            //eslint-disable-next-line no-param-reassign
            domElement = domElement.offsetParent;
        } while (domElement);

        return {
            top: top,
            left: left
        };
    },
    localizeNumber(number) { //english as fallback
        return number.toLocaleString([utils.getBrowserLanguageRaw(), 'en-US']);
    },
    formatTimeDuration(miliSecs) {
        var timeDiff = miliSecs;
        timeDiff /= 1000;
        const seconds = Math.round(timeDiff % 60);
        timeDiff = Math.floor(timeDiff / 60);
        const minutes = Math.round(timeDiff % 60);
        timeDiff = Math.floor(timeDiff / 60);
        const hours = Math.round(timeDiff % 24);
        timeDiff = Math.floor(timeDiff / 24);
        const days = timeDiff;

        // eslint-disable-next-line prefer-template
        let elapsed = (days > 0 ? days + 'd ' : '') + (hours > 0 ? hours + 'h ' : '') + (minutes > 0 ? minutes + 'm ' : '');
        elapsed = (elapsed !== '' ? elapsed : `${seconds}s`);
        return elapsed;
    },
    /* eslint-disable prefer-template */
    formatHHMMSS (num) {
        var secNum = num | 0; // don't forget the second param
        var hours = ~~(secNum / 3600);
        var minutes = ~~((secNum - (hours * 3600)) / 60);
        var seconds = secNum - (hours * 3600) - (minutes * 60);
        var time = '';

        if (hours) {
            time += hours + ':';
        }

        if (minutes < 10 && hours > 0) { minutes = '0' + minutes; }
        if (seconds < 10) { seconds = '0' + seconds; }

        time += minutes + ':' + seconds;
        return time;
    },
    getBrowserLanguageRaw() {
        var nav = window.navigator;
        var lang = nav.languages ? nav.languages[0] : null;
        lang = lang || nav.language || nav.browserLanguage || nav.userLanguage;

        return (lang || '').toLowerCase();
    },
    isIE() {
        const ua = window.navigator.userAgent;
        const msie = ua.indexOf('MSIE ');
        if (msie > 0) { // If Internet Explorer, return version number
            return parseInt(ua.substring(msie + 5, ua.indexOf('.', msie)), 10);
        }
        return false;
    },
    isEdge() {
        if (/Edge/.test(navigator.userAgent)) {
            return true;
        }
    },
    isEdgeOrIE() {
        return utils.isEdge() || utils.isIE();
    },
    getBrowserLanguage() {
        var lang = utils.getBrowserLanguageRaw();

        if (lang.indexOf('_') !== -1) {
            lang = lang.split('_').join('-');
        }

        if (lang !== 'pt-BR' && lang !== 'pt-br') { //we only support the Brazilian suffix
            lang = lang.substring(0, 2);
        }

        return (lang || '').toLowerCase();
    },
    getBrowserLanguages() {
        const langs = window.navigator.languages;
        if (utils.isIE()) {
            return [utils.getBrowserLanguage()];
        }
        return langs.map((lang) => {
            if (lang !== 'pt-BR' && lang !== 'pt-br') { //we only support the Brazilian suffix
                //eslint-disable-next-line no-param-reassign
                lang = lang.substring(0, 2);
            }
            return (lang || '').toLowerCase();
        });
    },
    getBrowserLocaleCountry() {
        const iso = utils.getBrowserLanguageRaw();

        if (!iso) {
            return '';
        }

        const isoSplit = iso.split('-');

        return (isoSplit.length > 1 ? isoSplit[1] : '').toLowerCase();
    },
    isLocaleCountry(country) {
        return utils.getBrowserLocaleCountry() === country;
    },
    getJsonFromUrl(hashBased) {
        var query;

        if (hashBased) {
            const pos = location.href.indexOf('?');
            if (pos === -1) {
                return [];
            }
            query = location.href.substr(pos + 1);
        } else {
            query = location.search.substr(1);
        }

        const result = {};

        query.split('&').forEach((part) => {
            if (!part) {
                return;
            }

            // eslint-disable-next-line
            part = part.split('+').join(' '); // replace every + with space, regexp-free version
            const eq = part.indexOf('=');
            let key = eq > -1 ? part.substr(0, eq) : part;
            const val = eq > -1 ? decodeURIComponent(part.substr(eq + 1)) : '';
            const from = key.indexOf('[');
            if (from === -1) {
                result[decodeURIComponent(key)] = val;
            } else {
                const to = key.indexOf(']');
                const index = decodeURIComponent(key.substring(from + 1, to));
                key = decodeURIComponent(key.substring(0, from));
                if (!result[key]) {
                    result[key] = [];
                }
                if (!index) {
                    result[key].push(val);
                } else {
                    result[key][index] = val;
                }
            }
        });
        return result;
    },
    getUrlParam(name) {
        var result = null;
        var urlJson = utils.getJsonFromUrl();
        if (urlJson[name]) {
            result = urlJson[name];
        }
        return result;
    },
    removeUrlParam(url, parameter) {
        var urlParts = url.split('?');
        var result = url;

        if (urlParts.length >= 2) {
            // Get first part, and remove from array
            const urlBase = urlParts.shift();

            // Join it back up
            const queryString = urlParts.join('?');

            const prefix = `${encodeURIComponent(parameter)}=`;
            const parts = queryString.split(/[&;]/g);

            // Reverse iteration as may be destructive
            for (let i = parts.length; i-- > 0;) {
                // Idiom for string.startsWith
                if (parts[i].lastIndexOf(prefix, 0) !== -1) {
                    parts.splice(i, 1);
                }
            }
            result = `${urlBase}?${parts.join('&')}`;
        }
        return result;
    },
    getCookie(cname) {
        var name = `${cname}=`;
        var decodedCookie = decodeURIComponent(document.cookie);
        var ca = decodedCookie.split(';');
        for (let i = 0; i < ca.length; i++) {
            let c = ca[i];
            while (c.charAt(0) === ' ') {
                c = c.substring(1);
            }
            if (c.indexOf(name) === 0) {
                return c.substring(name.length, c.length);
            }
        }
        return null;
    },
    setCookie(cname, cvalue, exdays = 0) {
        var d = new Date();
        d.setTime(d.getTime() + (exdays * 24 * 60 * 60 * 1000));
        //eslint-disable-next-line prefer-template
        const expires = 'expires=' + d.toUTCString();
        //eslint-disable-next-line prefer-template
        document.cookie = cname + '=' + cvalue + ';' + (exdays ? expires + ';' : '') + 'path=/';
    },
    getClientIdCookie() {
        return utils.getCookie('_ga');
    },
    stripGAVersioning(fullClientId) {
        if (!fullClientId) {
            return null;
        }
        const pieces = fullClientId.split('.');
        // current GA cookie format must have four dot delimited fields
        // GA_VERSION.COOKIE_PATH_IDENTIFIER.USER_ID.DATE
        if (pieces.length !== 4) {
            return null;
        }
        const user = pieces[pieces.length - 2];
        const date = pieces[pieces.length - 1];
        return `${user}.${date}`;
    },
    getFileExtension(filename) {
        const ext = filename ? filename.match(/.*\.(.*)$/) : null;
        return ext ? ext[1] : null;
    },
    detectIncognitoMode() {
        const params = utils.getJsonFromUrl();
        const cookieIsPresent = utils.getCookie('isNotIncognito');
        if (params['firstrun'] === '1') {
            //if first time run and cookie is not set we set it
            // (if this is new version install the cookie might already be set)
            if (!cookieIsPresent) {
                utils.setCookie('isNotIncognito', true, 999);
            }
            return false;
        //cookie is not present
        } else if (!cookieIsPresent) {
            utils.setCookie('isNotIncognito', true, 999);
            return true;
        }
        //cookie is present
        return false;
    },
};

export default utils;
