import 'blockadblock';
import store from '../../store';


// Function called if AdBlock is not detected
window.sentNotDetected = false;
function adBlockNotDetected(callback) {
    store.commit('setPresence', false);
    if (!window.sentNotDetected) {
        window.sentNotDetected = true;
    }
    if (typeof callback === 'function') {
        callback();
    }
}

// Function called if AdBlock is detected
window.sentDetected = false;
function adBlockDetected(callback) {
    store.commit('setPresence', true);
    if (!window.sentDetected) {
        window.sentDetected = true;
    }
    if (typeof callback === 'function') {
        callback();
    }
}

// Recommended audit because some AdBlockers block the file 'blockadblock.js'
// If the file is not called, the variable does not exist 'blockAdBlock'
// This means that AdBlock is present
if (typeof window.blockAdBlock === 'undefined') {
    adBlockDetected();
} else {
    window.blockAdBlock.onDetected(adBlockDetected);
    window.blockAdBlock.onNotDetected(adBlockNotDetected);
}

window.blockAdBlock.setOption({
    // debug: true,
    checkOnLoad: true,
    resetOnEnd: true,
    loopCheckTime: 20,
    loopMaxNumber: 200,
});

window.blockAdBlock.check();

export default {
    adBlockDetected,
    adBlockNotDetected
};

