<script>

import utils from '../utils';


export default {
    name: 'DropDown',
    props: {
        'options': {
            type: Array,
            required: true,
            default: () => ([]),
        },
        'title': {
            type: String,
            required: false,
            default: () => (''),
        },
        'default': {
            type: Number,
            required: false,
            default: () => (0),
        },
        'useDefaultOverride': {
            type: Boolean,
            required: false,
            default: () => (false),
        },
        'hideLabel': {
            type: Boolean,
            required: false,
            default: () => (false),
        },
        'selectStyle': {
            type: Object,
            required: false,
            default: () => ({}),
        },
        'labelStyle': {
            type: Object,
            required: false,
            default: () => ({}),
        },
    },
    data() {
        return {
            selectedOption: this.default,
        };
    },
    computed: {},
    watch: {
        default() {
            if (this.useDefaultOverride) {
                this.selectedOption = this.default;
            }
        },
    },
    methods: {
        select(event) {
            this.selectedOption = Number(event.target.value);
            if (this.selectedOption === this.default) {
                return;
            }
            this.$emit('select', this.selectedOption);
            if (this.useDefaultOverride) {
                this.selectedOption = this.default;
            }
        },
        displayOption(option) {
            return utils.upperCaseFirstChar(option);
        },
        isSelectedOption(index) {
            return this.selectedOption === index ? 'selected' : '';
        }
    },
};

</script>
<template>
<div class="drop-down">
    <label
        v-if="!hideLabel"
        :for="title"
        :style="labelStyle"
        class="wc3-btn">
        {{ title }}
    </label>
    <select
        :name="title"
        :style="selectStyle"
        class="drop"
        @input="select">
        <option
            :selected="isSelectedOption(index)"
            v-for="(option, index) in options"
            :key="index"
            :value="index"
            class="drop-option"
            >
            {{ displayOption(option) }}
        </option>
    </select>
  </div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';


label {
    display: block;
    cursor: inherit;
}

select {
    cursor: inherit;
}

.drop-down {
    .drop {
        background-color: #000080;
        // border-color: $yellow-gold;
        border-color: $grey-ml $grey-m $grey-mm $grey-ml;
        border: outset;
        border-radius: 3px;
        color: $gold-bold;
        &:active {
            border: none;
        }
        &:hover {
            color: white;
        }
        .drop-option {
        }
    }
}

</style>
