import GA from './tracking_providers/googleAnalytics';
import utils from '../utils';

/*eslint-disable max-len*/

class Tracking {
    constructor(config) {
        // general config
        this.GA = new GA(config);
        // page title is independent of the tracker being used
        this.config = config;
        this.userIdDimension = 1;
        this.dateDimension = 2;
        this.userVisitCountDimension = 3;
        this.googleOptimizeDimension = 4;
        const userVistCount = Number(window.localStorage.getItem('uvck'));
        const validNum = Number.isInteger(userVistCount);
        if (!validNum) {
            window.localStorage.setItem('uvck', 1);
        }
    }

    // @param {object} gaPropertyConfig - property specific config
    createGATracker(GA_PRODUCT_CODE, GOOGLE_OPTIMIZE_ID, gaPropertyConfig) {
        this.GA.createTracker(GA_PRODUCT_CODE, GOOGLE_OPTIMIZE_ID, gaPropertyConfig);
        const { href } = window.location;
        const staging = href.includes('staging') ? 'staging-landing-page' : null;
        // User to GA facing data
        const options = {};
        options.location = `${document.location.origin}${document.location.pathname}`;
        const urlParams = utils.getJsonFromUrl();
        // Goolge optimize requires the utm experiment id for reporting
        Object.keys(urlParams).forEach((paramName) => {
            if (paramName.match('utm')) {
                options.location += `&${paramName}=${urlParams[paramName]}`;
            }
        });
        options.page = staging || 'landing-page';
        this.set(options);
    }
    getClientId() {
        if (!this.GA) {
            return null;
        }
        return this.GA.getClientId();
    }

    setCustomDimension({ dimensionNumber, value }) {
        var gaTrackers = this.GA.getAllTrackers();
        //eslint-disable-next-line
        for (let key in gaTrackers) {
            //set custom dimensions here
            gaTrackers[key]('set', `dimension${dimensionNumber}`, value);
        }
    }

    send(options) {
        const gaTrackers = this.GA.getAllTrackers();
        this.setCustomDimension(this.dateDimension, Date.now());

        this.setCustomDimension(this.userVisitCountDimension,
            Number(window.localStorage.getItem('uvck')));
        let key;
        //eslint-disable-next-line
        for (key in gaTrackers) {
            gaTrackers[key]('send', options);
        }
    }

    set(options) {
        var key;
        var gaTrackers = this.GA.getAllTrackers();
        //eslint-disable-next-line
        for (key in gaTrackers) {
            gaTrackers[key]('set', options);
        }
    }

    setScreen(screenName) {
        this.set({
            screenName: screenName
        });
    }

    setPage(pageUrl) {
        this.set({
            page: pageUrl
        });
    }

    sendEvent(options) {
        options.hitType = 'event';
        this.send(options);
    }

    screenViewHit(screenName) {
        this.setScreen(screenName);
        this.send({
            screenName: screenName,
            hitType: 'screenview'
        });
    }

    pageViewHit(pageUrl) {
        this.setPage(pageUrl);
        this.send({
            hitType: 'pageview',
            page: pageUrl
        });
    }

    appLoad(screenName) {
        var options = {
            eventCategory: 'appLoad',
            eventAction: screenName,
            eventLabel: null,
            eventValue: null,
        };

        this.sendEvent(options);
    }

    incognitioMode() {
        var options = {
            eventCategory: 'incognitioMode',
            eventAction: null,
            eventLabel: null,
            eventValue: null,
            nonInteraction: true,
        };

        this.sendEvent(options);
    }

    usabilityAudit(action, label, value) {
        var options = {
            eventCategory: 'usabilityAudit',
            eventAction: action,
            eventLabel: label,
            eventValue: value
        };

        this.sendEvent(options);
    }

    gameStart(action, label, value) {
        var options = {
            eventCategory: 'gameStart',
            eventAction: action,
            eventLabel: label,
            eventValue: value
        };

        this.sendEvent(options);
    }

    gameEnd(action, label, value) {
        var options = {
            eventCategory: 'gameEnd',
            eventAction: action,
            eventLabel: label,
            eventValue: value
        };

        this.sendEvent(options);
    }

    viewSinglePlayer() {
        var options = {
            eventCategory: 'viewSinglePlayer',
        };

        this.sendEvent(options);
    }

    viewCredits() {
        var options = {
            eventCategory: 'viewCredits',
        };

        this.sendEvent(options);
    }

    viewMultiPlayer() {
        var options = {
            eventCategory: 'viewMultiPlayer',
        };

        this.sendEvent(options);
    }

    setUsername(username) {
        var options = {
            eventCategory: 'setUsername',
            eventLabel: username,
        };

        this.sendEvent(options);
    }
}

export default Tracking;
