<script>
import Vue from 'vue';
import utils from '../utils';
import unitsIndex from '../units/index';
import unitAttributes from '../unitAttributes';
import Unit from './Unit';
import ActionMenu from './ActionMenu';
import gameOverPopover from './gameOverPopover';
import GameBottomMenu from './GameBottomMenu';
import unitSoundInfo from '../units/unitSoundInfo';
// import unitAbilities from '../units/unitAbilities';
import abilitySoundInfo from '../units/abilitySoundInfo';
import constants from '../constants';

/*eslint-disable no-underscore-dangle*/

export default {
    name: 'BattleView',
    components: {
        'Unit': Unit,
        'ActionMenu': ActionMenu,
        'game-over-popover': gameOverPopover,
        GameBottomMenu,
    },
    props: ['teams', 'players', 'units', 'user'],
    data() {
        return {
            attributeHeaders: this.$store.state.attributeHeaders,
            deadUnits: {},
            createdUnits: {},
            gameStateUnits: this.$store.state.gameState.units,
            selectedUnitId: '',
            unitsIndex,
            initialized: false,
            pendingAction: 'what',
            didWin: null,
            showGameOver: false,
            selectedUnitActionSoundPath: '',
            selectedUnitSoundInfo: {},
        };
    },
    methods: {
        isEnemy(unitId) {
            return Boolean(!this.user.team.units[unitId]);
        },
        isUserUnit(unitId) {
            return Boolean(this.user.units[unitId]);
        },
        playVictoryMusic(race) {
            const usedRace = utils.upperCaseAll(race);
            const pathSrc = constants['SOUND_GENERAL'][usedRace]['VICTORY'];
            return this.$playSound(pathSrc);
        },
        playDefeatMusic(race) {
            const usedRace = utils.upperCaseAll(race);
            const pathSrc = constants['SOUND_GENERAL'][usedRace]['DEFEAT'];
            return this.$playSound(pathSrc);
        },
        getUnitSoundInfo(race, unitName) {
            let info = unitSoundInfo[unitName];
            if (info.alias) {
                info = unitSoundInfo[info.alias];
                info.name = info.alias;
            } else {
                info.name = unitName;
            }
            return info;
        },
        getUnitSoundPath(race, unitName) {
            const info = this.getUnitSoundInfo(race, unitName);
            return `${constants['SOUNDS_UNITS'][race]}/${info.name}/${info.name}`;
        },
        playDeath({ gameId, race }) {
            let unitName = unitsIndex.getUnitAttributeById(gameId, 'unit');
            unitName = utils.getNameFromEnum(unitName).replace(/\s/g, '');
            const soundInfo = this.getUnitSoundInfo(race, unitName);
            if (soundInfo.death === null) {
                return Promise.resolve();
            }
            const death = 'Death.wav';
            if (soundInfo.death === undefined && !soundInfo.alias) {
                return this.$playSound(this.getUnitSoundPath(race, unitName) + death);
            }
            // a uniquely specfied death sound from another unit (not an alias)
            return this.$playSound(this.getUnitSoundPath(race, soundInfo.death) + death);
        },
        // user action handlers
        unitClick(info) {
            const { action, unitId, unitActionSoundPath, soundInfo } = info;
            this.selectedUnitId = unitId;
            this.pendingAction = action;
            this.selectedUnitActionSoundPath = unitActionSoundPath;
            this.selectedUnitSoundInfo = soundInfo;
        },
        gameMenuUnitClick() {
            this.performAction({
                unitAId: this.selectedUnitId,
                unitBId: this.selectedUnitId,
                action: this.pendingAction,
            });
        },
        targetMe(unitId) {
            this.performAction({ unitAId: this.selectedUnitId, unitBId: unitId, action: this.pendingAction });
        },
        setAction({ action, noTarget }) {
            this.pendingAction = action;
            if (noTarget) {
                this.performAction({ unitAId: this.selectedUnitId, action });
            }
        },
        getAttackDamageApplied(unitAId, unitBId) {
            const unitA = this.gameStateUnits[unitAId].gameUnit;
            const unitB = this.gameStateUnits[unitBId].gameUnit;
            const position = unitB.modifiedPosition || unitB.defaultPosition;
            const attackLevel = unitA[`${position}AttackLevel`];
            if (attackLevel === 0) {
                return 0;
            }
            const attackLevelMax = unitA[`${position}AttackLevelMax`];
            const diff = attackLevelMax - attackLevel;
            const attackEnum = unitA[`${position}AttackEnum`];
            const armorEnum = unitB.armorEnum;
            const damageTypeMods = unitB.getDamageTypeModifier(attackEnum);
            // calculate damage absortion multiplier
            let absorbtionMultiplier = unitAttributes.attack_type_vs_armor_type[attackEnum][armorEnum] * damageTypeMods.multiply;
            // this value is a global damage type multiplier
            absorbtionMultiplier *= unitB.damageTakenMultiplier;
            // calculate base attack level
            let usedAttackLevel = attackLevel + utils.getRandomInt(diff);
            const attackMods = unitA.getAttributeModifier('attackLevel');
            // use attackers base damage as the value to multiply by
            usedAttackLevel *= attackMods.multiply;
            // then add attack bonus damage
            usedAttackLevel += attackMods.add;
            usedAttackLevel += damageTypeMods.add;
            let usedArmorLevel = unitB.armorLevel;
            const armorMods = unitB.getAttributeModifier('armorLevel');
            usedArmorLevel *= armorMods.multiply;
            usedArmorLevel += armorMods.add;
            const armorReduction = 1 - unitAttributes.armor_reduction(usedArmorLevel);
            return usedAttackLevel * absorbtionMultiplier * armorReduction;
        },
        removeUnitHealth(unitId, hitPoints) {
            if (hitPoints === 0) {
                return;
            }
            const unit = this.gameStateUnits[unitId].gameUnit;
            Vue.set(unit, 'hitPoints', unit.hitPoints - hitPoints);
            if (unit.hitPoints <= 0) {
                unit.alive = false;
                this.playDeath({ gameId: unit.gameId, race: unit.race });
                this.$store.dispatch('removeUnit', { id: unitId });
            }
        },
        addUnitHealth(unitId, hitPoints) {
            if (hitPoints === 0) {
                return;
            }
            const unit = this.gameStateUnits[unitId].gameUnit;
            const testVal = unit.hitPoints + hitPoints;
            if (testVal > 0 && testVal <= unit.hitPointsMax) {
                Vue.set(unit, 'hitPoints', unit.hitPoints + hitPoints);
                // deals with negative vals
            } else if (testVal < 0) {
                Vue.set(unit, 'hitPoints', 0);
                // this.battleMatch.removeUnit(unitId);
            } else {
                Vue.set(unit, 'hitPoints', unit.hitPointsMax);
            }
        },
        addUnitMana(unitId, manaPoints) {
            if (manaPoints === 0) {
                return;
            }
            const unit = this.gameStateUnits[unitId].gameUnit;
            const testVal = unit.manaPoints + manaPoints;
            if (testVal > 0 && testVal <= unit.manaPointsMax) {
                Vue.set(unit, 'manaPoints', unit.manaPoints + manaPoints);
                // deals with negative vals
            } else if (testVal < 0) {
                Vue.set(unit, 'manaPoints', 0);
            } else {
                Vue.set(unit, 'manaPoints', unit.manaPointsMax);
            }
        },
        removeUnitMana(unitId, manaPoints) {
            if (manaPoints === 0) {
                return;
            }
            const unit = this.gameStateUnits[unitId].gameUnit;
            if (unit.manaPoints - manaPoints > 0) {
                Vue.set(unit, 'manaPoints', unit.manaPoints - manaPoints);
            } else {
                Vue.set(unit, 'manaPoints', 0);
            }
        },
        async attackUnit(unitAId, unitBId) {
            const damage = this.getAttackDamageApplied(unitAId, unitBId);
            this.selectedUnit.autoAttackMods.forEach((autoAttackMod) => {
                if (!autoAttackMod.call) {
                    return;
                }
                const didEffect = autoAttackMod.call({
                    unitId: unitAId,
                    targetUnitId: unitBId,
                    gameStateUnits: this.gameStateUnits,
                    $store: this.$store,
                });
                if (!didEffect) {
                    return;
                }
                let fileName;
                const tasks = [];
                const soundInfo = abilitySoundInfo[autoAttackMod.name];
                if (!soundInfo) {
                    return;
                }
                if (soundInfo.caster) {
                    fileName = soundInfo.caster;
                    tasks.push(() => {
                        this.$playSound(`${constants.SPELLS[this.selectedUnit.race]}/${autoAttackMod.name}/${fileName}.wav`);
                    });
                }
                if (soundInfo.target) {
                    fileName = soundInfo.target;
                    tasks.push(() => {
                        this.$playSound(`${constants.SPELLS[this.selectedUnit.race]}/${autoAttackMod.name}/${fileName}.wav`);
                    });
                }
                const reducer = (promiseChain, currentTask) => promiseChain.then(currentTask);
                tasks.reduce(reducer, Promise.resolve());
            });
            if (this.selectedUnitSoundInfo.attack1) {
                const info = this.selectedUnitSoundInfo;
                const tasks = [];
                const sounds = [];
                // random first part of attack (only one if melee)
                let key = `attack${utils.getRandomInt(info.attackMax, info.attackMin)}`;
                sounds.push(info[key][0]);
                if (info.attack1.length > 1) {
                    // random second part of attack (if ranged)
                    key = `attack${utils.getRandomInt(info.attackMax, info.attackMin)}`;
                    sounds.push(info[key][1]);
                }
                if (info.attack1.length > 2) {
                    // random third part of attack (if ranged)
                    key = `attack${utils.getRandomInt(info.attackMax, info.attackMin)}`;
                    sounds.push(info[key][1]);
                }
                for (let i = 0; i < sounds.length; i++) {
                    const currTask = () => {
                        if (i === sounds.length - 1) {
                            return this.$playSound(`${constants.SOUND_GENERAL.WEAPON}/${sounds[i]}.wav`).then(() => {
                                this.removeUnitHealth(unitBId, damage);
                            });
                        }
                        return this.$playSound(`${constants.SOUND_GENERAL.WEAPON}/${sounds[i]}.wav`);
                    };
                    tasks.push(currTask);
                }
                const reducer = (promiseChain, currentTask) => promiseChain.then(currentTask);
                tasks.reduce(reducer, Promise.resolve());
                return;
            }
            this.removeUnitHealth(unitBId, damage);
        },
        castAbility({ ability, unitAId, unitBId, abilityName }) {
            // TODO FIX
            const cost = Number.isInteger(ability.cost) ? ability.cost : 100;
            // if (this.selectedUnit.manaPoints < cost) {
            //     return;
            // }
            this.removeUnitMana(this.selectedUnitId, cost);
            ability.call({
                unitId: unitAId,
                targetUnitId: unitBId,
                gameStateUnits: this.gameStateUnits,
                $store: this.$store,
            });
            const tasks = [];
            const soundInfo = abilitySoundInfo[abilityName];
            if (soundInfo.caster) {
                const caster = soundInfo.caster;
                tasks.push(() => {
                    this.$playSound(`${constants.SPELLS[this.selectedUnit.race]}/${abilityName}/${caster}.wav`);
                });
            }
            if (soundInfo.target) {
                const target = soundInfo.target;
                tasks.push(() => {
                    this.$playSound(`${constants.SPELLS[this.selectedUnit.race]}/${abilityName}/${target}.wav`);
                });
            }
            if (soundInfo.loop) {
                const loop = soundInfo.loop;
                tasks.push(() => {
                    this.$playSoundForDuration(`${constants.SPELLS[this.selectedUnit.race]}/${abilityName}/${loop}.wav`, ability.duration);
                });
            }
            const reducer = (promiseChain, currentTask) => promiseChain.then(currentTask);
            tasks.reduce(reducer, Promise.resolve());
        },
        performAction({ unitAId, unitBId, action }) {
            this.pendingAction = null;
            // determine unit audio response
            let min;
            let max;
            let int;
            let affirm;
            if (action === 'attack') {
                if (unitAId === unitBId) {
                    return;
                }
                if (this.selectedUnitSoundInfo.yesAttackMax) {
                    min = this.selectedUnitSoundInfo.yesAttackMin;
                    max = this.selectedUnitSoundInfo.yesAttackMax;
                    int = utils.getRandomInt(max, min);
                    affirm = `YesAttack${int}.wav`;
                } else {
                    min = this.selectedUnitSoundInfo.yesMin;
                    max = this.selectedUnitSoundInfo.yesMax;
                    int = utils.getRandomInt(max, min);
                    affirm = `Yes${int}.wav`;
                }
                this.$playSound(this.selectedUnitActionSoundPath + affirm);
                this.attackUnit(unitAId, unitBId);
                return;
            }
            min = this.selectedUnitSoundInfo.yesMin;
            max = this.selectedUnitSoundInfo.yesMax;
            int = utils.getRandomInt(max, min);
            affirm = `Yes${int}.wav`;
            this.$playSound(this.selectedUnitActionSoundPath + affirm);
            if (action === 'stop') {
                this.selectedUnit.abilityTarget = '';
                this.selectedUnit.autoAttackTarget = '';
            } else if (action === 'move') {
                // TODO
            } else if (action === 'holdPosition') {
                // TODO
            } else if (action === 'patrol') {
                // TODO
            }
            const ability = this.selectedUnit.abilitiesInfo.abilities[action];
            if (ability) {
                this.castAbility({ ability, unitAId, unitBId, abilityName: action });
            }
        },
        getUnitsList(units) {
            const ids = Object.keys(units);
            const filteredList = [];
            ids.forEach((key) => {
                if (key !== 'length') {
                    filteredList.push(units[key]);
                }
            });
            return filteredList;
        },
        getPlayersList(players) {
            const ids = Object.keys(players);
            const filteredList = [];
            ids.forEach((key) => {
                if (key !== 'length') {
                    filteredList.push(players[key]);
                }
            });
            return filteredList;
        },
        // initGameStateUnits(units) {
        //     const self = this;
        //     this.getUnitsList(units).forEach((/*matchInfoUnit*/) => {
        //         const { team, player, gameId, id } = matchInfoUnit;
        //         const playerId = player.id;
        //         const unit = self.getUnitGameStats(playerId, gameId);
        //         unit.player = player;
        //         unit.team = team;
        //         unit.id = id;
        //         unit.alive = true;
        //         Vue.set(self.gameStateUnits, unit.id, unit);
        //     });
        //     this.initialized = true;
        //     this.gameOverPromise = this.battleMatch.startMatch();
        //     this.gameOverPromise.then((didWin) => {
        //         const race = unitsIndex.races[self.user.race];
        //         self.showGameOver = true;
        //         self.didWin = didWin;
        //         if (didWin) {
        //             self.playVictoryMusic(race);
        //             return;
        //         }
        //         self.playDefeatMusic(race);
        //     });
        // },
        initAI() {
            // TODO: implement machine learning
            // hacky basic AI
            const team = this.user.team;
            const self = this;
            const otherTeams = this.teams.filter(t => t !== team);
            let otherTeam;
            for (let i = 0; i < otherTeams; i++) {
                otherTeam = otherTeams[i];
                const idsA = Object.keys(otherTeam.units).filter(id => id !== 'length');
                const idsB = Object.keys(team.units).filter(id => id !== 'length');
                idsA.forEach((idA, index) => {
                    const timer = setInterval(() => {
                        const unitA = idA;
                        const unitB = idsB[index];
                        const bothUnitsIdsPresent = Boolean(unitA && unitB);
                        const bothUnitsAlive = Boolean(self.units[unitA] && self.units[unitB]);
                        if (!(bothUnitsIdsPresent && bothUnitsAlive)) {
                            clearInterval(timer);
                            return;
                        }
                        self.attackUnit(unitA, unitB);
                    }, utils.getRandomInt(1, 5) * 1000);
                });
            }
        },
        gameEnd(didWin) {
            const race = unitsIndex[this.user.race];
            const matchMode = this.battleMatch.matchMode;
            this.$emit('gameEnd', JSON.stringify({ didWin, race, matchMode }));
        }
    },
    computed: {
        selectedUnitGameId() {
            if (!this.selectedUnitId) {
                return null;
            }
            return this.selectedUnit.gameId;
        },
        selectedUnit() {
            if (!this.selectedUnitId) {
                return { abilitiesInfo: { abilities: {} } };
            }
            return this.$store.state.gameState.units[this.selectedUnitId].gameUnit;
        },
        selectedUnitAttributes() {
            return this.selectedUnitGameId ? unitsIndex.getUnitAttributesById(this.selectedUnitGameId) : [];
        },
        battleStyle() {
            if (this.pendingAction !== 'pissed' && this.pendingAction !== 'what' && this.pendingAction) {
                return { cursor: `url(static/imgs/${'target-neutral'}.cur), auto` };
            }
            return {};
        },
    },
    created() {
        this.initAI();
        this.initialized = true;
        this.$store.commit('playBattleMusic');
    }
};

</script>
<template>
<div :style="battleStyle"
    class="battle">
    <span v-if="initialized" class="teams-container">
    <div class="team"
        v-for="(team) in teams"
        :key="team.id"
        :id="`team-${team.id}`">
        <div class="player"
            v-for="(player) in getPlayersList(team.players)"
            :id="`team-${team.id}-player-${player.id}`"
            :key="`team-${team.id}-player-${player.id}`">
            <Unit class="unit"
                v-for="unit in getUnitsList(player.units)"
                :unitId="unit.id"
                :attributes="unitsIndex.getUnitAttributesById(unit.gameId)"
                :gameUnit="unit.gameUnit"
                :key="unit.id"
                :pendingAction="pendingAction"
                :selectedUnitId="selectedUnitId"
                :isSelected="unit.id === selectedUnitId"
                :isEnemy="isEnemy(unit.id)"
                :isUserUnit="isUserUnit(unit.id)"
                :color="player.color"
                :allMods="unit.gameUnit.getAllModifiers()"
                @targetMe="targetMe"
                @click="unitClick">
            </Unit>
        </div>
    </div>
    <GameBottomMenu
        :attributes="selectedUnitAttributes"
        :selectedUnit="selectedUnit"
        :race="$store.getters.userRace"
        @click="gameMenuUnitClick">
        <ActionMenu
            :selectedUnit="selectedUnit"
            :isEnemy="isEnemy(selectedUnitId)"
            @setAction="setAction">
        </ActionMenu>
    </GameBottomMenu>
    </span>
    <game-over-popover v-if="showGameOver"
        @exit="gameEnd(didWin)"
        @showStats="gameEnd(didWin)"
        :didWin="didWin">
    </game-over-popover>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';

.battle {
    width: 100%;
    .teams-container {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        height: 100%;
        .team {
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            .player {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                width: 100%;
            }
        }
    }
}

</style>
