import Vue from 'vue';
import constants from '../../constants';
import didLoseMethods from './gameStateLoseCheckMethods';
import GameUnit from '../../units/GameUnit';

const { GAME_MODES } = constants;
const DEFAULT_PLAYER_MODE = GAME_MODES.PLAYER.K_ALL_TEAM_UNITS;
const DEFAULT_TEAM_MODE = GAME_MODES.TEAM.ALL_PLAYER_LOST;

function defaultPlayerMode() {
    return {
        mode: DEFAULT_PLAYER_MODE,
        specialUnitId: null,
        specialBuildingId: null,
        checkWhenUnitsAreRemoved: true,
        checkWhenBuildingsAreRemoved: false,
    };
}
function defaultTeamMode() {
    return {
        mode: DEFAULT_TEAM_MODE,
        specialPlayerId: null,
    };
}
function getDefaultPlayerColorsState() {
    const defaultPlayerColors = [
        'red',
        'blue',
        'yellow',
        'orange',
        'purple',
        'teal',
        'green',
        'gold',
        'black',
        'silver',
        'pink',
    ];
    const usedPlayerColors = {};
    const dict = {};
    defaultPlayerColors.forEach((color) => {
        usedPlayerColors[color] = false;
        dict[color] = true;
    });
    return {
        default: defaultPlayerColors,
        used: usedPlayerColors,
        get open() {
            return defaultPlayerColors.filter(color => usedPlayerColors[color] === false);
        },
        dict,
    };
}
/*eslint-disable no-shadow*/
function getDefaultState() {
    return {
        // online or offline
        local: true,
        units: { length: 0 },
        buildings: { length: 0 },
        teams: { length: 0 },
        players: { length: 0 },
        playerNames: {},
        teamNames: {},
        // used for default names
        teamIndex: 1,
        playerIndex: 1,
        gameOver: false,
        hostPlayerId: '',
        playerColors: getDefaultPlayerColorsState(),
        banned: {},
    };
}

function checkForDuplicateName(nameState, name) {
    let count = 0;
    let usedName = name;
    let hasCheckedForDuplicate = false;
    while (!hasCheckedForDuplicate) {
        if (nameState[usedName]) {
            count += 1;
            usedName = `${name} Copycat ${count}`;
        } else {
            hasCheckedForDuplicate = true;
        }
    }
    return usedName;
}

const state = getDefaultState();
const mutations = {
    resetState(state) {
        Object.assign(state, getDefaultState());
    },
    setHostPlayerId(state, { id }) {
        Vue.set(state, 'hostPlayerId', id);
    },
    checkIfGameOver(state) {
        let counter = 0;
        Object.keys(state.teams).forEach((id) => {
            const team = state.teams[id];
            if (team.didLose) {
                counter += 1;
            }
        });
        if (counter > state.teams.length - 1) {
            Vue.set(state, 'gameOver', true);
        }
    },
    // win/lose conditions
    updatePlayerMode(state, { id, playerMode, specialUnitId, specialBuildingId }) {
        const player = state.players[id];
        const err = new Error('updatePlayerMode');
        if (!player) {
            err.message = `Player with id: ${id} not found`;
            throw err;
        }
        if (GAME_MODES.SPECIAL_PLAYER_MODES[playerMode]) {
            if (GAME_MODES.SPECIAL_UNIT_MODES[playerMode]) {
                if (!specialUnitId) {
                    err.message = `Missing required (for playerMode: ${playerMode}) @param specialUnitId, got: ${specialUnitId}`;
                    throw err;
                }
                Vue.set(player.gameMode, 'specialUnitId', specialUnitId);
            }
            if (GAME_MODES.SPECIAL_BUILDING_MODES[playerMode]) {
                if (!specialBuildingId) {
                    err.message = `Missing required (for playerMode: ${playerMode}) @param specialBuildingId, got: ${specialBuildingId}`;
                    throw err;
                }
                Vue.set(player.gameMode, 'specialBuildingId', specialBuildingId);
            }
        }
        Vue.set(player.gameMode, 'checkWhenUnitsAreRemoved', Boolean(GAME_MODES.UNIT_MODES[playerMode]));
        Vue.set(player.gameMode, 'checkWhenBuildingsAreRemoved', Boolean(GAME_MODES.BUILDING_MODES[playerMode]));
        Vue.set(player.gameMode, 'mode', playerMode);
    },
    // win/lose conditions
    updateTeamMode(state, { id, teamMode, specialPlayerId }) {
        const err = new Error('updateTeamMode');
        // if (GAME_MODES.TEAM[teamMode] === undefined) {
        //     err.message = `No such teamMode: ${JSON.stringify(teamMode)}`;
        //     throw err;
        // }
        const team = state.teams[id];
        if (!team) {
            err.message = `Team with id: ${id} not found`;
            throw err;
        }
        if (GAME_MODES.SPECIAL_TEAM_MODES[teamMode]) {
            if (!specialPlayerId) {
                err.message = `Missing required (for teamMode: ${teamMode}) @param specialPlayerId, got: ${specialPlayerId}`;
                throw err;
            }
            Vue.set(team.gameMode, 'specialPlayerId', specialPlayerId);
        }
        Vue.set(team.gameMode, 'mode', teamMode);
    },
    // used for default team names
    updateTeamIndex(state) {
        Vue.set(state, 'teamIndex', state.teamIndex + 1);
    },
    setTeamMaxPlayers(state, { id, max }) {
        const team = state.teams[id];
        const err = new Error('setTeamMaxPlayers');
        if (!team) {
            err.message = `Team with id: ${id} not found.`;
            throw err;
        }
        if (team.players.length > max) {
            err.message = `Cannot set max players to be less than the current number of players on team ${id}`;
            throw err;
        }
        Vue.set(team, 'maxPlayers', max);
    },
    // used for default palyer names
    updatePlayerIndex(state) {
        Vue.set(state, 'playerIndex', state.playerIndex + 1);
    },
    updateTeamName(state, { id, name }) {
        const team = state.teams[id];
        const oldName = team.name;
        Vue.delete(state.teamNames, oldName);
        const usedName = checkForDuplicateName(state.teamNames, name);
        Vue.set(team, 'name', usedName);
        Vue.set(state.teamNames, usedName, true);
    },
    addTeam(state, { name, id }) {
        let usedName = name || `Team ${state.teamIndex}`;
        usedName = checkForDuplicateName(state.teamNames, usedName);
        const team = {
            id,
            name: usedName,
            players: { length: 0 },
            units: { length: 0 },
            buildings: { length: 0 },
            gameMode: defaultTeamMode(),
            maxPlayers: Infinity,
            get didLose() {
                return didLoseMethods.team[this.gameMode.mode](this);
            },
        };
        Vue.set(state.teams, team.id, team);
        Vue.set(state.teams, 'length', state.teams.length + 1);
        Vue.set(state.teamNames, team.name, true);
    },
    removeTeam(state, { id }) {
        const team = state.teams[id];
        // team not found
        if (!team) {
            console.warn(`Team with id "${id}" not found.`);
            return;
        }
        if (Object.keys(team.players) > 0) {
            const err = new Error('removeTeam');
            err.message = 'Unable to remove team when team currently has players.';
            throw err;
        }
        Vue.delete(state.teams, id);
        Vue.set(state.teams, 'length', state.teams.length - 1);
        Vue.delete(state.teamNames, team.name);
    },
    updatePlayerName(state, { id, name }) {
        const player = state.players[id];
        const oldName = player.name;
        Vue.delete(state.playerNames, oldName);
        const usedName = checkForDuplicateName(state.playerNames, name);
        Vue.set(player, 'name', usedName);
        Vue.set(state.playerNames, usedName, true);
    },
    updatePlayerRace(state, { id, race }) {
        const player = state.players[id];
        Vue.set(player, 'race', race);
    },
    addPlayer(state, { user, name, id, teamId, race = 'Random', startingUnits = [], startingBuildings = [] }) {
        const team = state.teams[teamId];
        if (!team) {
            const err = new Error('addPlayer');
            err.message = `Team with id "${id}" not found.`;
            throw err;
        }
        if (team.players.length >= team.maxPlayers) {
            console.warn(`Can not add any additional players to team: ${id} with maxPlayers: ${team.maxPlayers}`);
            return;
        }
        let usedName = name || `Player ${state.playerIndex}`;
        usedName = checkForDuplicateName(state.playerNames, usedName);
        const player = {
            id,
            playerType: user ? 'user' : 'ai',
            name: usedName,
            units: { length: 0 },
            buildings: { length: 0 },
            team,
            race,
            startingUnits,
            startingBuildings,
            gameMode: defaultPlayerMode(),
            get didLose() {
                return didLoseMethods.player[this.gameMode.mode](this);
            },
            color: '',
        };
        Vue.set(team.players, player.id, player);
        Vue.set(team.players, 'length', team.players.length + 1);
        Vue.set(state.players, player.id, player);
        Vue.set(state.players, 'length', state.players.length + 1);
        Vue.set(state.playerNames, player.name, true);
    },
    removePlayer(state, { id }) {
        const player = state.players[id];
        if (!player) {
            console.warn(`Player with id "${id}" not found.`);
            return;
        }
        Vue.delete(state.players, id);
        Vue.set(state.players, 'length', state.players.length - 1);
        Vue.delete(state.playerNames, player.name);
        const team = player.team;
        Vue.delete(team.players, player.id);
        Vue.set(team.players, 'length', team.players.length - 1);
    },
    banPlayer(state, { id }) {
        Vue.set(state.banned, id, true);
    },
    updatePlayerColor(state, { id, color }) {
        const err = new Error('updatePlayerColor');
        const usedColor = color || state.playerColors.open.shift();
        if (state.playerColors.used[usedColor]) {
            err.message = `Can not set player color to already used color: ${usedColor}.`;
            throw err;
        }
        if (state.playerColors.dict === undefined) {
            err.message = `No such color: ${usedColor}.`;
            throw err;
        }
        const player = state.players[id];
        const oldColor = player.color;
        if (oldColor) {
            Vue.set(state.playerColors.used, oldColor, false);
        }
        Vue.set(player, 'color', usedColor);
        Vue.set(state.playerColors.used, usedColor, true);
    },
    addUnit(state, { playerId, id, unitId, gameUnit }) {
        const player = state.players[playerId];
        const team = player.team;
        const unit = {
            team,
            player,
            id,
            gameId: unitId,
            gameUnit: gameUnit || new GameUnit(unitId),
        };
        const units = state.units;
        Vue.set(units, id, unit);
        Vue.set(units, 'length', units.length + 1);
        Vue.set(team.units, id, unit);
        Vue.set(team.units, 'length', team.units.length + 1);
        Vue.set(player.units, id, unit);
        Vue.set(player.units, 'length', player.units.length + 1);
        return units[id];
    },
    removeUnit(state, { id }) {
        const unit = state.units[id];
        if (!unit) {
            console.warn(`Unit with id "${id}" not found.`);
            return;
        }
        const { team, player } = unit;
        Vue.delete(team.units, id);
        Vue.set(team.units, 'length', team.units.length - 1);
        Vue.delete(player.units, id);
        Vue.set(player.units, 'length', player.units.length - 1);
        Vue.delete(state.units, id);
        Vue.set(state.units, 'length', state.units.length - 1);
    },
    addBuilding(state, { playerId, id, buildingId }) {
        const player = state.players[playerId];
        const team = player.team;
        const building = {
            team,
            player,
            id,
            gameId: buildingId,
        };
        const buildings = state.buildings;
        Vue.set(buildings, id, building);
        Vue.set(buildings, 'length', buildings.length + 1);
        Vue.set(team.buildings, id, building);
        Vue.set(team.buildings, 'length', team.buildings.length + 1);
        Vue.set(player.buildings, id, building);
        Vue.set(player.buildings, 'length', player.buildings.length + 1);
    },
    removeBuilding(state, { id }) {
        const building = state.buildings[id];
        if (!building) {
            console.warn(`Building with id "${id}" not found.`);
            return;
        }
        const { team, player } = building;
        Vue.delete(team.buildings, id);
        Vue.set(team.buildings, 'length', team.buildings.length - 1);
        Vue.delete(player.buildings, id);
        Vue.set(player.buildings, 'length', player.buildings.length - 1);
        Vue.delete(state.buildings, id);
        Vue.set(state.buildings, 'length', state.buildings.length - 1);
    },
};
const actions = {
    addTeam(context, { name }) {
        const id = `team-${Math.random()}`;
        context.commit('addTeam', { id, name });
        context.commit('updateTeamIndex');
        return id;
    },
    removeTeam(context, { id }) {
        context.commit('removeTeam', { id });
    },
    addPlayer(context, { id, user, name, teamId, race, startingUnits = [], startingBuildings = [] }) {
        if (!teamId) {
            const err = new Error('addPlayer');
            err.message = `invalid @param teamId, got: ${teamId}`;
            throw err;
        }
        if (state.banned[id]) {
            console.warn(`Cannot add banned player: ${id}`);
            return;
        }
        const team = context.state.teams[teamId];
        if (team.players.length >= team.maxPlayers) {
            console.warn(`Can not add any additional players to team: ${id} with maxPlayers: ${team.maxPlayers}`);
            return;
        }
        //eslint-disable-next-line
        id = id || `${user ? 'user' : 'AI'}-${Math.random()}`;
        context.commit('addPlayer', {
            name,
            id,
            teamId,
            user,
            race,
            startingUnits,
            startingBuildings,
            color: '',
        });
        context.commit('updatePlayerColor', { id, color: undefined });
        context.commit('updatePlayerIndex');
        return id;
    },
    banPlayer(context, { id }) {
        context.commit('removePlayer', { id });
        context.commit('banPlayer', { id });
    },
    addUnit(context, { playerId, unitId }) {
        const err = new Error('addUnit');
        if (!playerId) {
            err.message = `invalid @param playerId, got: ${playerId}`;
            throw err;
        }
        if (!unitId) {
            err.message = `invalid @param unitId, got: ${unitId}`;
            throw err;
        }
        const id = `${unitId}-${Math.random()}`;
        context.commit('addUnit', { playerId, id, unitId });
        return id;
    },
    removeUnit(context, { id }) {
        const unit = state.units[id];
        if (!unit) {
            console.warn(`Unit with id "${id}" not found.`);
            return;
        }
        const { team, player } = unit;
        context.commit('removeUnit', { id });
        if (player.gameMode.checkWhenUnitsAreRemoved) {
            if (player.didLose) {
                if (team.didLose) {
                    context.commit('checkIfGameOver');
                }
            }
        }
    },
    addBuilding(context, { playerId, buildingId }) {
        const err = new Error('addBuilding');
        if (!playerId) {
            err.message = `invalid @param playerId, got: ${playerId}`;
            throw err;
        }
        if (!buildingId) {
            err.message = `invalid @param buildingId, got: ${buildingId}`;
            throw err;
        }
        const id = `${buildingId}-${Math.random()}`;
        context.commit('addBuilding', { playerId, id, buildingId });
        return id;
    },
    removeBuilding(context, { id }) {
        const building = state.buildings[id];
        if (!building) {
            console.warn(`building with id "${id}" not found.`);
            return;
        }
        const { team, player } = building;
        context.commit('removeBuilding', { id });
        if (player.gameMode.checkWhenBuildingsAreRemoved) {
            if (player.didLose) {
                if (team.didLose) {
                    context.commit('checkIfGameOver');
                }
            }
        }
    },
    // HELPER ACTIONS
    removeAllTeams(context, { keep = {} }) {
        const { state } = context;
        Object.keys(state.teams).forEach((teamId) => {
            if (teamId !== 'length' && !keep[teamId]) {
                const team = state.teams[teamId];
                if (team.players.length > 0) {
                    context.dispatch('removeAllPlayersFromTeam', { id: teamId });
                }
                context.commit('removeTeam', { id: teamId });
            }
        });
    },
    removeAllPlayers(context, { keep = {} }) {
        const { state } = context;
        Object.keys(state.players).forEach((playerId) => {
            if (playerId !== 'length' && !keep[playerId]) {
                context.commit('removePlayer', { id: playerId });
            }
        });
    },
    removeAllPlayersFromTeam(context, { id, keep = {} }) {
        const { state } = context;
        const team = state.teams[id];
        const err = new Error('removeAllPlayersFromTeam');
        if (!team) {
            err.message = `No such team with id: ${id}`;
            throw err;
        }
        Object.keys(team.players).forEach((playerId) => {
            if (playerId !== 'length' && !keep[playerId]) {
                context.commit('removePlayer', { id: playerId });
            }
        });
    },
    removeAllUnitsFromPlayer(context, { id, keep = {} }) {
        const { state } = context;
        const player = state.players[id];
        Object.keys(player.units).forEach((unitId) => {
            if (unitId == 'length' || keep[unitId]) {
                return;
            }
            context.commit('removeUnit', { id: unitId });
        });
    },
    removeAllUnitsFromTeam(context, { id, keep = {} }) {
        const { state } = context;
        const team = state.teams[id];
        Object.keys(team.units).forEach((unitId) => {
            if (unitId == 'length' || keep[unitId]) {
                return;
            }
            context.commit('removeUnit', { id: unitId });
        });
    },
    removeAllBuildingsFromPlayer(context, { id, keep = {} }) {
        const { state } = context;
        const player = state.players[id];
        Object.keys(player.buildings).forEach((buildingId) => {
            if (buildingId == 'length' || keep[buildingId]) {
                return;
            }
            context.commit('removeUnit', { id: buildingId });
        });
    },
    removeAllBuildingsFromTeam(context, { id, keep = {} }) {
        const { state } = context;
        const team = state.teams[id];
        Object.keys(team.buildings).forEach((buildingId) => {
            if (buildingId == 'length' || keep[buildingId]) {
                return;
            }
            context.commit('removeUnit', { id: buildingId });
        });
    },
};

const getters = {
    // teams() {
    //     const teams = {};
    //     Object.keys(s.teams).forEach((id) => {
    //         if (id === 'length') {
    //             return;
    //         }
    //         teams[id] = s.teams[id];
    //     });
    //     return teams;
    // },
    teamIds(s) {
        return Object.keys(s.teams).filter(id => id !== 'length');
    },
    playerIds(s) {
        return Object.keys(s.players).filter(id => id !== 'length');
    },
    unitIds(s) {
        return Object.keys(s.units).filter(id => id !== 'length');
    },
};

export default {
    state,
    mutations,
    actions,
    getters,
};
