import Vue from 'vue';
import Vuex from 'vuex';
import adblockDetection from './modules/adblockDetection';
import attributeHeaders from './modules/attributeHeaders';
import gameState from './modules/gameState';
import gameTime from './modules/gameTime';
import userSession from './modules/userSession';
import volume from './modules/volume';

Vue.use(Vuex);

const store = new Vuex.Store({
    modules: {
        adblockDetection,
        attributeHeaders,
        gameState,
        gameTime,
        userSession,
        volume,
    },
    strict: false,
});

export default store;
