<script>

import menuFrame from './menuFrame';
import menuChain from './menuChain';

export default {
    name: '',
    props: ['chainBackgroundColor'],
    components: {
        'menu-frame': menuFrame,
        'menu-chain': menuChain,
    },
    data() {
        return {
            userString0: 'Warcraft III',
            userString1: 'Ice Age',
            userString2: 'Single Player',
            userString3: 'Multiplayer',
            userString4: 'Credits',
        };
    },
    computed: {},
    methods: {
        emitShowSinglePlayerMenu() {
            this.$emit('showSinglePlayerMenu');
        },
        emitShowMultiPlayerMenu() {
            this.$emit('showMultiPlayerMenu');
        },
        emitShowCreditsMenu() {
            this.$emit('showCreditsMenu');
        },
    },
};

</script>
<template>
<div id="entry-menu">
    <div id="menu-chains-container">
        <menu-chain :backgroundColor="chainBackgroundColor"></menu-chain>
        <div class="spacer"></div>
        <menu-chain :backgroundColor="chainBackgroundColor"></menu-chain>
    </div>
    <menu-frame id="app-menu-container">
        <div id="text-container">
            <div id="title-container">
                <div id="title-1-container">
                    <div id="title-1" class="wc3-title-iron">{{userString0}}</div>
                </div>
                <div id="title-2" class="frozen-text">{{userString1}}</div>
            </div>
            <div id="menu-container">
                <div id="single-player"
                    class="wc3-btn"
                    @click="emitShowSinglePlayerMenu">
                    {{userString2}}
                </div>
                <div id="multiplayer"
                    class="wc3-btn"
                    @click="emitShowMultiPlayerMenu">
                    {{userString3}}
                </div>
                <div id="credit"
                    class="wc3-btn"
                    @click="emitShowCreditsMenu">
                    {{userString4}}
                </div>
            </div>
        </div>
    </menu-frame>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';

$chain-height: 196px;

#entry-menu {
    height: 100%;
    min-height: 600px;
    min-width: 800px;
    $title-font-size: 100px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    transition: all 0.7s linear;
    transition-duration: 0.7s;
    #menu-chains-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: absolute;
        z-index: 0;
        height: $chain-height;
        .spacer {
            width: 294px;
        }
    }
    #app-menu-container {
        margin-top: $chain-height;
        height: 660px;
        width: 800px;
        #text-container {
            padding: 30px;
            z-index: 1;
            display: flex;
            flex-direction: column;
            #title-container {
                z-index: 1;
                position: relative;
                #title-1-container {
                    position: relative;
                    #title-1 {
                        position: relative;
                        z-index: 1;
                        width: 100%;
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        font-size: $title-font-size;
                    }
                }
                #title-2 {
                    width: 100%;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    font-size: $title-font-size;
                }
            }
            #menu-container{
                z-index: 1;
                position: relative;
                margin-top: 15px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
            }
        }
    }
}

</style>
