import Vue from 'vue';

function getDefaultState() {
    return {
        userId: null,
        teamId: null,
        username: window.localStorage.getItem('username'),
        user: {},
    };
}

const mutations = {
    resetState(state) {
        Object.assign(state, getDefaultState());
    },
    setUser(s, user) {
        Vue.set(s, 'user', user);
    },
    setUserAttribute(s, attribute, value) {
        if (!attribute) {
            const err = new Error('setUserAttribute');
            err.message = `Invalid @param attribute: ${attribute}`;
            throw err;
        }
        Vue.set(s.user, attribute, value);
    },
    setUserRace(s, race) {
        Vue.set(s.user, 'race', race);
        window.localStorage.setItem('race', race);
    },
    setUserId(s, userId) {
        Vue.set(s, 'userId', userId);
    },
    setTeamId(s, teamId) {
        Vue.set(s, 'teamId', teamId);
    },
    setUsername(s, username) {
        const usedUsername = username || window.localStorage.getItem('username') || 'Player 1';
        Vue.set(s, 'username', usedUsername);
        window.localStorage.setItem('username', usedUsername);
    },
};

const actions = {
    setUser(context, { id, username }) {
        const user = context.rootState.gameState.players[id];
        const teamId = user.team.id;
        context.commit('setUser', user);
        context.commit('setUserId', user.id);
        context.commit('setTeamId', teamId);
        if (username) {
            context.commit('setUsername', username);
        }
    },
};

const getters = {
    isHost(s, _, rootState) {
        return s.userId === rootState.gameState.hostPlayerId;
    },
    userRace(s) {
        return s.user.race;
    },
};

export default {
    state: getDefaultState(),
    mutations,
    actions,
    getters
};
