<script>

export default {
    name: 'CooldownOverlay',
    props: {
        'degree': {
            type: Number,
            required: false,
            default: 0,
        },
        'radius': {
            type: Number,
            required: false,
            default: 55,
        }
    },
    components: {},
    data() {
        return {
            cx: this.radius,
            cy: this.radius,
            deg1: 0,
            deg2: 0,
            containerStyle: {
                'max-width': `${this.radius * 2}`,
                'max-height': `${this.radius * 2}`,
                'min-width': `${this.radius * 2}`,
                'min-height': `${this.radius * 2}`,
            },
            pie1Style: {
                'margin-bottom': `-${this.radius}px`,
                'max-width': `${this.radius * 2}`,
                'max-height': `${this.radius * 2}`,
                'min-width': `${this.radius * 2}`,
                'min-height': `${this.radius * 2}`,
            },
            pie2Style: {
                'margin-top': `-${this.radius}px`,
                'max-width': `${this.radius * 2}`,
                'max-height': `${this.radius * 2}`,
                'min-width': `${this.radius * 2}`,
                'min-height': `${this.radius * 2}`,
            },
        };
    },
    computed: {
        r() {
            return this.radius;
        },
        x1() {
            return this.cx + (this.r * Math.cos(this.deg1 * (Math.PI / 180)));
        },
        y1() {
            return this.cy + (this.r * Math.sin(this.deg1 * (Math.PI / 180)));
        },
        d1() {
            const { x1, y1, r } = this;
            return `M${r},${r} L${r * 2},${r} A${r},${r} 1 0,1 ${x1},${y1} z`;
        },
        x2() {
            return this.cx + (this.r * Math.cos(this.deg2 * (Math.PI / 180)));
        },
        y2() {
            return this.cy + (this.r * Math.sin(this.deg2 * (Math.PI / 180)));
        },
        d2() {
            const { x2, y2, r } = this;
            return `M${r},${r} L${r * 2},${r} A${r},${r} 1 0,1 ${x2},${y2} z`;
        },
    },
    methods: {
        rightClick(e) {
            this.$emit('rightClick');
            e.preventDefault();
        },
    },
    watch: {
        degree(newVal) {
            if (newVal <= 180) {
                this.deg1 = 180 - newVal;
                this.deg2 = 180;
            } else if (newVal <= 360) {
                this.deg1 = 0;
                this.deg2 = 360 - newVal;
            } else {
                const usedVal = newVal % 360;
                if (usedVal <= 180) {
                    this.deg1 = 180 - usedVal;
                    this.deg2 = 180;
                } else if (usedVal <= 360) {
                    this.deg1 = 0;
                    this.deg2 = 360 - usedVal;
                }
            }
        }
    },
};

</script>
<template>
<div class="cd-container" :style="containerStyle" @contextmenu="rightClick">
    <svg class="pie1" :style="pie1Style">
        <path ref="pie1" :d="d1"></path>
    </svg>
    <svg class="pie2" :style="pie2Style">
        <path ref="pie2" :d="d2"></path>
    </svg>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';

.cd-container {
    display: flex;
    // clip-path: inset(75px 75px 75px 75px);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    svg {
        fill: rgba(0, 0, 0, 0.41);
        overflow-x: hidden;
        overflow-y: hidden;
    }
    svg path {
        transition: d 0.84s;
    }
    .pie1 {
        transform: scaleX(-1) scaleY(-1);
        fill: rgba(0, 0, 0, 0.41);

    }
    .pie2 {
        fill: rgba(0, 0, 0, 0.41);
    }
}

</style>
