<script>
import woodFrame from './woodFrame';

export default {
    name: 'game-over-popover',
    components: {
        'wood-frame': woodFrame,
    },
    props: ['didWin'],
    data() {
        return {
            userString0: 'Stats',
            userString1: 'Exit',
            userString2: 'Victory!',
            userString3: 'Defeat!',
        };
    },
    computed: {},
    methods: {
        clickStats() {
            this.$emit('showStats');
        },
        clickExit() {
            this.$emit('exit');
        },
    },
};

</script>
<template>
<div id="game-over">
    <wood-frame :width="420" :height="180">
        <div id="game-over-messages">
            <div class="game-status">{{ didWin ? userString2 : userString3 }}</div>
            <div class="game-over-button" @click="clickStats">{{userString0}}</div>
            <div class="game-over-button" @click="clickExit">{{userString1}}</div>
        </div>
    </wood-frame>
</div>
</template>
<style lang="scss">
@import '../stylesheets/vars';
@import '../stylesheets/colors';


#game-over {
    position: absolute;
    z-index: 5;
    left: 40vw;
    top: 40vh;
    #game-over-messages {
        display: flex;
        flex-direction: column;
        justify-content: center;
        width: 420px;
        height: 150px;
        margin-top: -15px;
        .game-status {
            margin-left: auto;
            margin-right: auto;
            margin-bottom: 5px;
            color: yellow;
            font-family: 'Times New Roman';
        }
        .game-over-button {
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-content: center;
            align-items: center;
            width: 200px;
            height: 28px;
            border-radius: 4px;
            border: 1px solid $yellow-gold;
            border-bottom-color: $yellow-gold;
            color: yellow;
            font-weight: 100;
            background: url('../static/imgs/wood.jpg') rgba(97, 45, 29, 0.7);
            background-size:cover;
            background-blend-mode: multiply;
            margin-top: 10px;
            margin-left: auto;
            margin-right: auto;
            font-family: 'Times New Roman';
            &:hover {
                color: white;
            }
        }
    }
}


</style>
