
class GA {
    constructor(config) {
        //config represents the details of the current web app
        this.gaBlockerDetected = config.gaBlockerDetected || null;
        delete config.gaBlockerDetected;
        this.config = config;
        this.trackersMap = {};
        this.trackers = {};
        this.beforeSendHookCallbacks = [];
        //eslint-disable-next-line no-undef
        this.ga = ga;
        this.counter = 0;
    }
    static blacklistCategory({ sendOptions, blacklistOptions }) {
        const { category } = blacklistOptions;
        if (category) {
            const blockCat = category.values.includes(sendOptions.eventCategory) ||
                category.values.includes('*');
            const exceptCat = category.except.includes(sendOptions.eventCategory);
            if (blockCat && !exceptCat) {
                return true;
            }
        }
    }
    registerBeforeSendHookCallback(callback) {
        if (typeof callback !== 'function') {
            //eslint-disable-next-line
            throw 'callback is not a function';
        }
        this.beforeSendHookCallbacks.push(callback);
    }
    beforeSendHook(tracker) {
        const self = this;
        const originalSendHitTask = tracker.get('sendHitTask');
        tracker.set('sendHitTask', (model) => {
            let updatedModel = model;
            self.beforeSendHookCallbacks.forEach((callback) => {
                updatedModel = callback(updatedModel);
            });
            originalSendHitTask(updatedModel);
        });
    }
    getTracker(userTrackerName) {
        return this.trackers[userTrackerName];
    }
    getTrackerProperty(userTrackerName, property) {
        const tracker = this.ga.getByName(userTrackerName);
        return tracker ? tracker.get(property) : null;
    }
    getAllTrackers() {
        return this.trackers;
    }
    getClientId() {
        return this.ga.getAll()[0].get('clientId');
    }
    createTracker(trackerId, optimizeId, options = {}) {
        /* eslint-disable prefer-rest-params */
        var trackerName = `t${this.counter}`;
        var useName = options.userTrackerName || trackerName;
        var config = this.config;
        var blacklistOptions = options.blacklist || null;
        var self = this;
        this.counter += 1;
        //not using for now
        delete config.campaign;
        config.name = trackerName;

        const wrapper = function wrapper() {
            if (blacklistOptions && arguments[1] && arguments[1].hitType === 'event') {
                //eslint-disable-next-line max-len
                if (self.constructor.blacklistCategory({ sendOptions: arguments[1], blacklistOptions })) {
                    return;
                }
            }
            arguments[0] = `${useName}.${arguments[0]}`;
            self.ga.apply(self.ga, arguments);
        };

        try {
            this.ga('create', trackerId, 'auto', config);
            if (optimizeId && !this.gaBlockerDetected) {
                this.ga('require', optimizeId);
            }
            //always use https to ensure redirects don't happen
            this.ga('set', 'forceSSL', true);
            this.trackers[useName] = wrapper;
            this.ga(() => {
                self.ga.getAll().forEach((tracker) => {
                    self.beforeSendHook(tracker);
                    self.ga('set', 'dimension1', tracker.get('clientId'));
                    const userTracker = self.ga.getByName(useName);
                    //configured properties that cannot be set during instatiation
                    userTracker.set(config);
                    wrapper.ready = true;
                    wrapper.data = userTracker;
                });
            });
        } catch (err) {
            console.error('Google analytics script tag misssing from document.');
            this.trackers[useName] = null;
        }

        return this.trackers[useName];
    }
}

export default GA;
