export default {
    // methods that check if individual player lost
    player: {
        // player level checks
        0: p => p.units.length === 0,
        1: p => p.buildings.length === 0,
        2: p => p.buildings.length === 0 && p.units.length === 0,
        3: p => p.buildings.length === 0 || p.units.length === 0,
        6: p => p.units[p.gameMode.specialUnitId] === undefined,
        7: p => p.buildings[p.gameMode.specialBuildingId] === undefined,
        8: p => p.buildings[p.gameMode.specialBuildingId] === undefined && p.units[p.gameMode.specialUnitId] === undefined,
        9: p => p.buildings[p.gameMode.specialBuildingId] === undefined || p.units[p.gameMode.specialUnitId] === undefined,
        // team level checks
        10: p => p.team.units.length === 0,
        11: p => p.team.buildings.length === 0,
        12: p => p.team.buildings.length === 0 && p.team.units.length === 0,
        13: p => p.team.buildings.length === 0 || p.team.units.length === 0,
        16: p => p.team.units[p.gameMode.specialUnitId] === undefined,
        17: p => p.team.buildings[p.gameMode.specialBuildingId] === undefined,
        18: p => p.team.buildings[p.gameMode.specialBuildingId] === undefined && p.team.units[p.gameMode.specialUnitId] === undefined,
        19: p => p.team.buildings[p.gameMode.specialBuildingId] === undefined || p.team.units[p.gameMode.specialUnitId] === undefined,
    },
    // methods that check if an individual team lost
    team: {
        20: (t) => {
            let counter = 0;
            let player;
            Object.keys(t.players).forEach((id) => {
                player = t.players[id];
                if (player.didLose) {
                    counter += 1;
                }
            });
            return counter === t.players.length;
        },
        21: t => Object.keys(t.players)
            .map(id => t.players[id])
            .find(player => player.didLose) !== undefined,
        22: t => t.players[t.gameMode.specialPlayerId].didLose,
    },
};
