<script>

import Autocast from './Autocast';
import CooldownOverlay from './CooldownOverlay';
import utils from '../utils';
import constants from '../constants';

export default {
    name: 'ActionMenu',
    props: {
        'selectedUnit': {
            type: Object,
            required: false,
            default: () => ({ abilitiesInfo: { abilities: {} } }),
        },
        'isEnemy': {
            type: Boolean,
            required: false,
            default: () => (false),
        }
    },
    components: {
        Autocast,
        CooldownOverlay,
    },
    data() {
        return {
            utils,
            base: constants.IMAGE_BASE,
            abilityBoxWidth: 60,
            abilityBoxHeight: 60,
            testAutocast: false,
            rowLength: 4,
            uiGeneralActions: {
                move: { displayName: 'Move', noTarget: false },
                stop: { displayName: 'Stop', noTarget: true },
                holdPosition: { displayName: 'HoldPosition', noTarget: true },
                attack: { displayName: 'Attack', noTarget: false },
                patrol: { displayName: 'Patrol', noTarget: false },
            },
            row1: {
                move: { displayName: 'Move', noTarget: false },
                stop: { displayName: 'Stop', noTarget: true },
                holdPosition: { displayName: 'HoldPosition', noTarget: true },
                attack: { displayName: 'Attack', noTarget: false },
            },
            autocastSrc: constants.SOUND_GENERAL.AUTOCAST,
            degree: 0,
        };
    },
    computed: {
        abilities() {
            if (this.isEnemy) {
                return {};
            }
            return this.selectedUnit.abilitiesInfo.abilities;
        },
        abilityNames() {
            return Object.keys(this.abilities);
        },
        numEmptyTiles() {
            let num = 12;
            if (this.isEnemy) {
                return num;
            }
            num -= Object.keys(this.uiGeneralActions).length;
            num -= this.abilityNames.length;
            return num;
        },
    },
    methods: {
        getUiGeneralActionPath(action) {
            return `${this.base}/BTN${action}.png`;
        },
        getUiAbilityStylePath(ability) {
            if (!ability || !this.abilities[ability]) {
                return '';
            }
            let pas = '';
            let auto = '';
            const abilityInfo = this.abilities[ability];
            if (abilityInfo.passive) {
                pas += 'PAS';
            } else if (abilityInfo.autocast) {
                auto += 'Auto';
            }
            return `${this.base}/${pas}BTN${abilityInfo.srcName || ability}${auto}.png`;
        },
        getUiGeneralActionStyle(action) {
            return {
                'background-image': `url(${this.getUiGeneralActionPath(action)})`,
            };
        },
        getUiAbilityStyle(ability) {
            return {
                'background-image': `url(${this.getUiAbilityStylePath(ability)})`,
            };
        },
        abilityClick(abilityName) {
            let abilityInfo;
            if (this.uiGeneralActions[abilityName]) {
                abilityInfo = this.uiGeneralActions[abilityName];
            } else {
                abilityInfo = this.abilities[abilityName];
            }
            this.$emit('setAction', { action: abilityName, noTarget: abilityInfo.noTarget });
        },
        toggleAutocast(abilityName) {
            if (!this.abilities[abilityName].autocast) {
                return;
            }
            this.selectedUnit.autocastStatus[abilityName] = !this.selectedUnit.autocastStatus[abilityName];
            Object.keys(this.selectedUnit.autocastStatus).forEach((otherAbility) => {
                if (otherAbility !== abilityName) {
                    this.selectedUnit.autocastStatus[otherAbility] = false;
                }
            });
            if (this.hasAutocastOn(abilityName)) {
                this.$playSound(this.autocastSrc);
            }
            this.$emit('toggleAutoCast', abilityName);
        },
        hasAutocastOn(abilityName) {
            return this.selectedUnit.autocastStatus[abilityName];
        },
    },
    created() {
    },
};

</script>
<template>
<div id="action-menu">
    <svg id="grid-svg" width="100%" height="100%" xmlns="http://www.w3.org/2000/svg">
      <defs>
        <pattern id="grid" width="62" height="62" patternUnits="userSpaceOnUse">
          <path d="M 62 0 L 0 0 0 62" fill="black" stroke="#4a4a4a" stroke-width="2"/>
        </pattern>
      </defs>
      <rect width="100%" height="100%" fill="url(#grid)" />
    </svg>
    <div id="actions-container">
        <div
            v-for="(actionInfo, actionName) in uiGeneralActions"
            v-tooltip="actionInfo.displayName"
            :style="getUiGeneralActionStyle(actionInfo.displayName)"
            :key="actionName"
            class="action"
            @click="abilityClick(actionName)"
            >
        </div>
        <div v-for="abilityName in abilityNames"
            v-tooltip="abilityName"
            :style="getUiAbilityStyle(abilityName)"
            :key="abilityName"
            class="action"
            @click="abilityClick(abilityName)"
            >
            <Autocast v-if="abilities[abilityName].autocast"
                @toggleAutocast="toggleAutocast(abilityName)"
                @click="abilityClick(abilityName)"
                :width="abilityBoxWidth"
                :height="abilityBoxHeight"
                :toggleOn="hasAutocastOn(abilityName)"
                :ability="abilityName">
            </Autocast>
            <CooldownOverlay
                @rightClick="toggleAutocast(abilityName)"
                :degree="degree"
                class="cooldown"
                >
            </CooldownOverlay>
        </div>
    </div>
</div>
</template>
<style lang="scss">
