    flex-direction: row;
    border: 1px solid $grey-d;
    width: $action-menu-width;
    height: $action-menu-height;
    margin-right: 2px;
    #grid-svg {
        z-index: 0;
    }
    #actions-container {
        z-index: 1;
        position: absolute;
        display: flex;
        flex-wrap: wrap;
        align-content: start;
        width: $action-menu-width;
        height: $action-menu-height;
    }
    .action {
        display: flex;
        min-width: $ability-tile-dimension;
        max-width: $ability-tile-dimension;
        min-height: $ability-tile-dimension;
        max-height: $ability-tile-dimension;
        background-color: black;
        background-size: contain;
        &:hover{
            box-shadow: inset 0px 0px 5px 6px  rgba(168, 245, 143, 0.79);
        }
        margin: 1px;
    }
    .empty-tile {
        // background-color: black;
        // border: 2px inset grey;
        min-width: $ability-tile-dimension;
        max-width: $ability-tile-dimension;
        min-height: $ability-tile-dimension;
        max-height: $ability-tile-dimension;
    }
}

</style>
