<script>
import Player from './Player';
import LobbySlot from './LobbySlot';

export default {
    name: 'Team',
    components: {
        Player,
        LobbySlot,
    },
    props: ['team'],
    data() {
        return {
            userString0: 'Open Slots: ',
            userString1: 'No Team Size Limit',
            lobbySlotStatuses: [],
        };
    },
    computed: {
        players() {
            return Object.keys(this.team.players)
                .filter(playerId => playerId !== 'length')
                .map(playerId => this.team.players[playerId]);
        },
        playerCap() {
            return this.team.maxPlayers !== Infinity;
        },
        openSlots() {
            if (!this.playerCap) {
                // no need to show open slots
                // when there is no limit
                return 0;
            }
            return this.team.maxPlayers - this.team.players.length;
        },
        isHost() {
            return this.$store.getters.isHost;
        },
        maxPlayers() {
            return this.team.maxPlayers;
        },
        teamId() {
            return this.team.id;
        },
        openColors() {
            return this.$store.state.gameState.playerColors.open;
        },
    },
    watch: {
        maxPlayers(newVal) {
            this.lobbySlotStatuses = this.lobbySlotStatuses.slice(0, newVal);
        },
    },
    methods: {
        hostPlayerSlotSelection({ selectionIndex, id }) {
            ({
                0: this.kick,
                1: this.close,
                2: this.computer,
                3: this.reserve,
                4: this.ban,
            })[selectionIndex](id);
        },
        hostLobbySlotSelection({ statusIndex, index }) {
            this.lobbySlotStatuses[index] = statusIndex;
            ({
                1: this.close,
                2: this.computer,
                3: this.reserve,
            })[statusIndex]();
        },
        kick(id) {
            this.$store.commit('removePlayer', { id });
        },
        close(id) {
            this.$store.commit('removePlayer', { id });
            if (!this.playerCap) {
                return;
            }
            this.$store.commit('setTeamMaxPlayers', { id: this.teamId, max: this.team.maxPlayers - 1 });
        },
        computer(id) {
            if (id) {
                this.$store.commit('removePlayer', { id });
            }
            //this.$store.dispatch('addComputerPlayer')
            this.$store.dispatch('addPlayer', { teamId: this.teamId, name: 'Computer' });
        },
        reserve(id) {
            this.$store.commit('removePlayer', { id });
            //this.$store.dispatch('reserveLobbySlot')
        },
        ban(id) {
            this.$store.dispatch('banPlayer', { id });
        },
        closeTeam() {
            this.$emit('closeTeam', this.teamId);
        },
        isPlayerHost(id) {
            return this.$store.getters.isHost && (id === this.$store.state.userSession.userId);
        },
    },
};

</script>
<template>
<div class="team-lobby">
    <div class="info">
        <div class="team-name">
            {{ team.name }}
        </div>
        <div class="player-cap">
            {{ playerCap ? userString0 + openSlots : userString1 }}
        </div>
    </div>
    <div class="slots-container">
        <Player
            v-for="player in players"
            :key="player.id"
            :player="player"
            :isHost="isPlayerHost(player.id)"
            :userIsHost="isHost"
            :openColors="openColors"
            @hostPlayerSlotSelection="hostPlayerSlotSelection">
        </Player>
        <LobbySlot
            v-for="i in openSlots"
            :index="i"
            :statusIndex="lobbySlotStatuses[i] || 0"
            :key="i"
            @hostLobbySlotSelection="hostLobbySlotSelection">
        </LobbySlot>
    </div>
</div>
</template>
