<script>

export default {
    name: 'Health',
    props: {
        min: {
            type: Number,
            required: true
        },
        now: {
            type: Number,
            required: true
        },
        max: {
            type: Number,
            required: true
        }
    },
    computed: {
        percentWidth() {
            const range = this.max - this.min;
            const pos = this.now - this.min;
            return `${Math.round(100 * (pos / range))}%`;
        },
        style() {
            return {
                width: this.percentWidth,
                'background-color': this.getColorFromRange(parseInt(this.percentWidth.substr(0, this.percentWidth.length - 1), 10))
            };
        }
    },
    methods: {
        limit(num, min, max) {
            if (num < min) return min;
            if (num > max) return max;
            return num;
        },
        getColorFromRange(percStr) {
            const num = percStr;
            let r = 0;
            let g = 0;
            let b = 0;
            const abs = Math.abs(num - 50);
            /* Red under 50 */
            if (num < 50) {
                r = 1;
                if (r > 0.5) {
                    g = 1 - (abs * 0.02);
                }
                /* Green Over 50 */
            } else {
                g = 1;
                if (g > 0.5) {
                    r = 1 - (abs * 0.02);
                }
            }
            r = this.limit(r, 0, 1);
            g = this.limit(g, 0, 1);
            b = this.limit(b, 0, 1);
            return `rgb(${Math.floor(r * 255)}, ${Math.floor(g * 255)}, ${Math.floor(b * 255)})`;
        }
    },
};

</script>
<template>
<div class="progress">
    <div
      class="progress-bar progress-bar-striped progress-bar-animated"
      v-bind:aria-valuenow="now"
      v-bind:aria-valuemin="min"
      v-bind:aria-valuemax="max"
      v-bind:style="style">
      {{ percentWidth }}
    </div>
</div>
</template>
<style lang="scss" scoped>
