<script>

import MenuPortrait from './MenuPortrait';
import UnitInfoStatus from './UnitInfoStatus';
import Inventory from './Inventory';
import utils from '../utils';

export default {
    name: 'HumanMenu',
    props: {
        'selectedUnit': {
            type: Object,
            required: false,
            default: () => ({ abilitiesInfo: { abilities: {} } }),
        },
        'attributes': {
            type: Array,
            required: false,
            default: () => ([]),
        },
        'race': {
            type: String,
            required: false,
            default: () => ('Human'),
        },
    },
    components: {
        MenuPortrait,
        UnitInfoStatus,
        Inventory,
    },
    data() {
        return {
            attributeHeaders: this.$store.state.attributeHeaders,
        };
    },
    computed: {
        portrait() {
            return this.getAttribute('portrait');
        },
        name() {
            return utils.getNameFromEnum(this.getAttribute('unit'));
        },
        attackType() {
            return this.getAttribute('ground_attack_type');
        },
        weaponType() {
            return this.getAttribute('ground_weapon_type');
        },
        groundAttack() {
            return this.getAttribute('ground_attack');
        },
        groundAttackMaximum() {
            return this.getAttribute('ground_attack_maximum');
        },
        secondGroundAttack() {
            return this.getAttribute('second_ground_attack');
        },
        secondGroundAttackMaximum() {
            return this.getAttribute('second_ground_attack_maximum');
        },
        secondGroundAttackType() {
            return this.getAttribute('second_ground_attack_type');
        },
        airAttack() {
            return this.getAttribute('air_attack');
        },
        airAttackMaximum() {
            return this.getAttribute('air_attack_maximum');
        },
        airAttackType() {
            return this.getAttribute('air_attack_type');
        },
        armorType() {
            return this.getAttribute('armor_type');
        },
        armor() {
            return this.getAttribute('armor');
        },
        //default starting value
        hitPoints() {
            return this.selectedUnit.hitPoints;
        },
        hitPointsMaximum() {
            return this.hitPointsMaximumOverride || this.getAttribute('hit_points');
        },
        manaPoints() {
            return this.selectedUnit.manaPoints;
        },
        manaPointsMaximum() {
            return this.selectedUnit.manaPointsMax;
        },
        bonusGroundAttack() {
            const attackLevel = this.groundAttack;
            let bonusDamage = attackLevel;
            if (!attackLevel) {
                return;
            }
            const attackMods = this.selectedUnit.getAttributeModifier('attackLevel');
            bonusDamage *= attackMods.multiply;
            bonusDamage += attackMods.add;
            bonusDamage -= attackLevel;
            return Math.round(bonusDamage);
        },
        bonusSecondGroundAttack() {
            const attackLevel = this.groundAttack;
            let bonusDamage = attackLevel;
            if (!attackLevel) {
                return;
            }
            const attackMods = this.selectedUnit.getAttributeModifier('secondGroundAttackLevel');
            bonusDamage *= attackMods.multiply;
            bonusDamage += attackMods.add;
            bonusDamage -= attackLevel;
            return Math.round(bonusDamage);
        },
        bonusAirAttack() {
            const attackLevel = this.groundAttack;
            let bonusDamage = attackLevel;
            if (!attackLevel) {
                return;
            }
            const attackMods = this.selectedUnit.getAttributeModifier('airAttackLevel');
            bonusDamage *= attackMods.multiply;
            bonusDamage += attackMods.add;
            bonusDamage -= attackLevel;
            return Math.round(bonusDamage);
        },
        bonusArmor() {
            const armorMods = this.selectedUnit.getAttributeModifier('armorLevel');
            const armorLevel = this.armor;
            let bonusArmor = armorLevel;
            bonusArmor *= armorMods.multiply;
            bonusArmor += armorMods.add;
            bonusArmor -= armorLevel;
            return Math.round(bonusArmor);
        },
        allMods() {
            return this.selectedUnit.getAllModifiers();
        },
    },
    methods: {
        getAttribute(enumName) {
            const val = this.attributes[this.attributeHeaders.headerEnums[enumName]];
            if (val === undefined || val === null) {
                return '';
            }
            return val;
        },
        click() {
            this.$emit('click');
        }
    },
};

</script>
<template>
<div id="human-menu">
    <MenuPortrait v-if="name"
        :portrait="portrait"
        :hitPoints="hitPoints"
        :hitPointsMaximum="hitPointsMaximum"
        :manaPoints="manaPoints"
        :manaPointsMaximum="manaPointsMaximum"
        @click="click">
    </MenuPortrait>
    <div id="info-status-container">
        <div rowspan="2"
            valign="top"
            class="human-menu-outline-left">
        </div>
        <UnitInfoStatus v-if="name"
            :name="name"
            :groundAttack="groundAttack"
            :groundAttackMaximum="groundAttackMaximum"
            :attackType="attackType"
            :secondGroundAttack="secondGroundAttack"
            :secondGroundAttackMaximum="secondGroundAttackMaximum"
            :secondGroundAttackType="secondGroundAttackType"
            :airAttack="airAttack"
            :airAttackMaximum="airAttackMaximum"
            :airAttackType="airAttackType"
            :armor="armor"
            :armorType="armorType"
            :bonusGroundAttack="bonusGroundAttack"
            :bonusSecondGroundAttack="bonusSecondGroundAttack"
            :bonusAirAttack="bonusAirAttack"
            :bonusArmor="bonusArmor"
            :allMods="allMods">
        </UnitInfoStatus>
        <div rowspan="2"
            valign="top"
            class="human-menu-outline-right">
        </div>
    </div>
    <Inventory></Inventory>
    <div rowspan="2"
        valign="top"
        class="human-menu-outline-left">
    </div>
    <div :class="{
            'human-action-menu-container': race === 'Human',
            'orc-action-menu-container': race === 'Orc',
            'nightelf-action-menu-container': race === 'Nightelf',
            'undead-action-menu-container': race === 'Undead',
        }">
        <slot></slot>
    </div>
</div>
</template>
