    getSpellPath({ spellName, missile, target, caster, loop, fade }) {
        const str = `${base}/${this[spellName].race}/${spellName}`;
        if (missile) {
            return str + this[spellName].missile + type;
        }
        if (target) {
            return str + this[spellName].target + type;
        }
        if (caster) {
            return str + this[spellName].caster + type;
        }
        if (loop) {
            return str + this[spellName].loop + type;
        }
        if (fade) {
            return str + this[spellName].fade + type;
        }
        return str + this[spellName].path + type;
    }
